.progressIndicator__percentageContainer
{
    position:           absolute;
    text-align:         center;
    top:                0;
    bottom:             0;
    left:               0;
    right:              0;
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;
}

.progressIndicator__percentage
{

    font-size:          30px;
    letter-spacing:     0.1px;
    margin-top:         3px;
}

.progressIndicator__spinner
{

    width:                84px;
    height:               84px;
    min-height:           84px;
    border-width:         8px;



}

.progressIndicator__spinner:after
    {
        width:              100px;
        height:             100px;
        border-width:       8px;
        -webkit-animation:          progressIndicator__defaultSpinnerRingRotate .8s infinite ease-in-out;
                animation:          progressIndicator__defaultSpinnerRingRotate .8s infinite ease-in-out;
        left:               -8px;
        top:                -8px;
    }

@-webkit-keyframes progressIndicator__defaultSpinnerRingRotate
{
    0%
    {
        -webkit-transform: rotateZ(0deg) scale(1);
                transform: rotateZ(0deg) scale(1); /* scale to voerride default spinner setup */
    }
    100%
    {
        -webkit-transform: rotateZ(360deg) scale(1);
                transform: rotateZ(360deg) scale(1);
    }
}

@keyframes progressIndicator__defaultSpinnerRingRotate
{
    0%
    {
        -webkit-transform: rotateZ(0deg) scale(1);
                transform: rotateZ(0deg) scale(1); /* scale to voerride default spinner setup */
    }
    100%
    {
        -webkit-transform: rotateZ(360deg) scale(1);
                transform: rotateZ(360deg) scale(1);
    }
}
