.statusIndicator__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.statusIndicator__default:last-child
    {
        margin-bottom:  0;
    }

.statusIndicator__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;

    width:              auto;
    height:             24px;

    padding:            0 8px;

    border-radius:      12px;

    font-size:          14px;
    line-height:        16px;
    text-align:         center;

    overflow:           hidden;
    text-overflow:      ellipsis;
}

.statusIndicator__status__active, .statusIndicator__status__promoted
{
    color:              #1bbab0;
    background-color:   #bbeae7;
}

.statusIndicator__status__alert
{
    color:              #ff965a;
    background-color:   #ffdfcd;
}

.statusIndicator__status__critical
{
    color:              #ff5460;
    background-color:   #ffcccf;
}

.statusIndicator__status__deactivated
{
    color:              #597494;
    background-color:   #c7d0db;
}
