.uploader__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.uploader__default:last-child
    {
        margin-bottom:  0;
    }

.uploader__default
{
    width:  auto;
}

.uploader__loading
{
    width:  100%;
}

.uploader__loading .uploader__uploaderButton
    {
        pointer-events: none;
    }

.uploader__uploaded
{
    width:  100%;
}

.uploader__loading .uploader__uploaderButton, .uploader__uploaded .uploader__uploaderButton
    {
        border-top-right-radius:    0;
        border-bottom-right-radius: 0;
    }

.uploader__loading .uploader__iconWithTooltip, .uploader__uploaded .uploader__iconWithTooltip
    {
        display:    block;
        width:      100%;
    }

.uploader__loading .uploader__previewTooltip, .uploader__uploaded .uploader__previewTooltip
    {
        width:  calc(100% - 32px);
    }

.uploader__disabled .uploader__uploadedButton, .uploader__disabled .uploader__loadingOverlay
    {
        background:         #bae3f6;
        color:              #52b9e9;
        pointer-events:     none;
        opacity:            1;
    }

.uploader__previewDisabled .uploader__uploaderButton
    {
        pointer-events: none;
    }

.uploader__input
{
    position:   absolute;
    left:       -999999px;
}

.uploader__previewTooltip
{
    -webkit-box-flex:   1;
    -webkit-flex:   1 0 auto;
        -ms-flex:   1 0 auto;
            flex:   1 0 auto;
    width:  100%;
}

.uploader__uploaderButton
{
    width:                   100%;
    max-width:               100%;
    text-align:              left;
    text-transform:          none;
    display:                 block;
}

.uploader__buttonsContainer
{
    display:   -webkit-box;
    display:   -webkit-flex;
    display:   -ms-flexbox;
    display:   flex;
}

.uploader__buttonsContainer > *
    {
        margin-bottom: 0;
    }

.uploader__uploadedButton
{
    -webkit-box-flex:                       0;
    -webkit-flex:                       0 0 auto;
        -ms-flex:                       0 0 auto;
            flex:                       0 0 auto;
    width:                      40px;
    height:                     40px;
    padding:                    0;
    border:                     none;
    background-color:           #19a2e2;
    border-top-right-radius:    2px;
    border-bottom-right-radius: 2px;
}

.uploader__uploadedButton:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.uploader__uploadedButton:focus::after
        {
            display: none;
        }

.uploader__uploadedButton:hover
    {
        background:     #30abe4;
        color:          #ffffff;
    }

.uploader__loadingOverlay
{
    display:                    -webkit-inline-box;
    display:                    -webkit-inline-flex;
    display:                    -ms-inline-flexbox;
    display:                    inline-flex;
    -webkit-box-align:                center;
    -webkit-align-items:                center;
        -ms-flex-align:                center;
            align-items:                center;
    -webkit-box-pack:            center;
    -webkit-justify-content:            center;
        -ms-flex-pack:            center;
            justify-content:            center;
    position:                   relative;
    min-width:                  40px;
    height:                     40px;
    background-color:           #19a2e2;
    vertical-align:             top;
    border-top-right-radius:    2px;
    border-bottom-right-radius: 2px;
}

.uploader__loadingOverlay:focus
    {
        outline:    none;
    }

.uploader__spinner
{
    border-color:           rgba(255, 255, 255, 0.25);
}

.uploader__spinner::after
    {
        border-top-color:   #ffffff;
    }

.uploader__iconWithTooltip
{
    display:    table;
    width:      auto;
}
