.dragNDrop__default
{
}

.dragNDrop__dropzoneIsVisible .dragNDrop__content
    {
        opacity:    0.2;
    }

.dragNDrop__dropzoneContentContainer
{
    display:                -webkit-box;
    display:                -webkit-flex;
    display:                -ms-flexbox;
    display:                flex;

    -webkit-box-orient:         vertical;

    -webkit-box-direction:         normal;

    -webkit-flex-direction:         column;

        -ms-flex-direction:         column;

            flex-direction:         column;
    -webkit-box-align:            center;
    -webkit-align-items:            center;
        -ms-flex-align:            center;
            align-items:            center;
    -webkit-box-pack:        center;
    -webkit-justify-content:        center;
        -ms-flex-pack:        center;
            justify-content:        center;

    position:               absolute;

    margin-left:                   -8px;

    height:                 calc(100% + 10px);
    min-height:             80px;
    width:                  calc(100% - 20px);

    z-index:                999;

    border:                 3px dashed #19a2e2;
    border-radius:          5px;

    background:             rgba(255, 255, 255, 0.5);

    pointer-events:         none;

    top:                    50%;
    -webkit-transform:              translateY(-50%);
            transform:              translateY(-50%);


}

.dragNDrop__message
{
    width:          100%;

    color:          rgba(25, 162, 226, 0.7);

    font-size:      24px;
    line-height:    28px;

    text-align:     center;
}

.dragNDrop__message::before
    {
        content:                '';
        display:                block;

        width:                  100%;
        height:                 40px;

        margin-bottom:          6px;

        background-image:       url(data:image/svg+xml;base64,);
        background-position:    center;
        background-repeat:      no-repeat;

        opacity:                0.7;
    }

.dragNDrop__spinner
{
    border-color:   rgba(25, 162, 226, 0.25);
}

.dragNDrop__spinner::after
    {
        border-top-color:   #19a2e2;
    }
