.pageFooter__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.pageFooter__default:last-child
    {
        margin-bottom:  0;
    }

.pageFooter__default
{
    -webkit-box-flex:		0;
    -webkit-flex:		0 0 auto;
        -ms-flex:		0 0 auto;
            flex:		0 0 auto;
    width:		100%;

    margin:		0;

    background:	var(--GS-1);
    color:		#ffffff;
}

.pageFooter__content
{
    -webkit-box-sizing:	border-box;
            box-sizing:	border-box;
    width:		80%;
    min-width:	320px;
    max-width:	960px;
    margin:		20px auto;

    opacity:	0.3;
}
