.sliderGroup__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.sliderGroup__default:last-child
    {
        margin-bottom:  0;
    }

.sliderGroup__default .sliderGroup__flexContainer
    {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
    }

.sliderGroup__default .sliderGroup__flexContainer:not(:last-child)
        {
            margin-bottom:  10px;
        }

.sliderGroup__default .sliderGroup__stepLabelsContainer
    {
        position:       relative;

        width:          80px;
        margin-top:     8px;
        margin-bottom:  8px;
    }

.sliderGroup__default .sliderGroup__labelWrapper
    {
        position:        absolute;
        -webkit-transform:       translateY( 50% );
                transform:       translateY( 50% );
        max-width:       100%;

        overflow:       hidden;
        word-wrap:  normal;
        text-overflow:  ellipsis;
    }

.sliderGroup__default .sliderGroup__stepLabel
    {
        font-size:      14px;
        line-height:    16px;
        color:          #98a8bc;
    }

.sliderGroup__default .sliderGroup__slidersContainer
    {
        display:   -webkit-box;
        display:   -webkit-flex;
        display:   -ms-flexbox;
        display:   flex;
        -webkit-box-flex:      1;
        -webkit-flex:      1 0 auto;
            -ms-flex:      1 0 auto;
                flex:      1 0 auto;
        min-width: 0;
    }

