        display:         -ms-flexbox;
        display:         flex;
        -webkit-box-flex:            1;
        -webkit-flex:            1 0 0%;
            -ms-flex:            1 0 0%;
                flex:            1 0 0%;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
            -ms-flex-pack: center;
                justify-content: center;
    }

.sliderGroup__default .sliderGroup__sliderLabelContainer
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
        margin-left:    80px;
    }

