.datePickerHeader__default
{
    position:                   relative;

    padding-top:                24px;
    padding-right:              24px;
    padding-bottom:             16px;
    padding-left:               24px;

    background-color:           #19a2e2;

    border-top-left-radius:     2px;
    border-top-right-radius:    2px;
}

.datePickerHeader__default .datePickerHeader__date
    {
        color:          #ffffff;
        font-size:      22px;
        font-weight:    300;
        line-height:    24px;

        margin-bottom:  0;
        margin-top:     16px;
        padding-top:    24px;
        position:       relative;

        display:        inline-block;
        width:          50%;
    }

.datePickerHeader__default .datePickerHeader__year
    {
        font-size:      13px;
        font-weight:    400;
        line-height:    16px;

        position:       absolute;
        top:            0;
        left:           0;
    }

.datePickerHeader__default .datePickerHeader__buttonsWrapper
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-pack:    justify;
        -webkit-justify-content:    space-between;
            -ms-flex-pack:    justify;
                justify-content:    space-between;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
        -webkit-align-content:      center;
            -ms-flex-line-pack:      center;
                align-content:      center;
    }

.datePickerHeader__default .datePickerHeader__prev, .datePickerHeader__default .datePickerHeader__next
    {
        width:          24px;
        height:         24px;
    }

.datePickerHeader__default .datePickerHeader__prev:focus::after, .datePickerHeader__default .datePickerHeader__next:focus::after
        {
            -webkit-box-shadow:   0 0 0 2px rgba(255, 255, 255, 0.3);
                    box-shadow:   0 0 0 2px rgba(255, 255, 255, 0.3);
        }
