.sorter__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.sorter__default:last-child
    {
        margin-bottom:  0;
    }

.sorter__default
{
    width:  auto;
    font:   inherit;
}

.sorter__default:hover .sorter__up, .sorter__default:hover .sorter__down, .sorter__default.sorter__fakeHovered .sorter__up, .sorter__default.sorter__fakeHovered .sorter__down
        {
            opacity:    0.5;
        }

.sorter__sorterVisible
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-orient:     horizontal;
    -webkit-box-direction:     normal;
    -webkit-flex-direction:     row;
        -ms-flex-direction:     row;
            flex-direction:     row;
    -webkit-box-pack:    start;
    -webkit-justify-content:    flex-start;
        -ms-flex-pack:    start;
            justify-content:    flex-start;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;

    cursor:             pointer;
}

.sorter__sort__asc .sorter__up
    {
        opacity:    1 !important;
    }

.sorter__sort__desc .sorter__down
    {
        opacity:    1 !important;
    }

.sorter__content
{
    -webkit-box-flex:   1;
    -webkit-flex:   1 0 auto;
        -ms-flex:   1 0 auto;
            flex:   1 0 auto;
}

.sorter__sorter
{
    display:        -webkit-box;
    display:        -webkit-flex;
    display:        -ms-flexbox;
    display:        flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
        -ms-flex-direction: column;
            flex-direction: column;

    -webkit-box-flex:           0;

    -webkit-flex:           0 0 auto;

        -ms-flex:           0 0 auto;

            flex:           0 0 auto;

    margin-left:    10px;

}

.sorter__down
{
    margin-top: -11px;
}
