.module__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.module__default:last-child
    {
        margin-bottom:  0;
    }

.module__default .module__default
    {
        border-radius:  0;
    }

.module__default .module__default .module__header:after
        {
            display:    none;
        }

.module__default .module__default + .module__default
    {
        border-top: none;
        margin-top: -16px;
    }

.module__default .module__header, .module__default .module__content
    {
        position:   relative;
        margin:     0;

        padding-top:     24px;
        padding-right:   32px;
        padding-bottom:  8px;
        padding-left:    32px;
    }

.module__default .module__header
    {
        -webkit-transition:         background-color 200ms ease-in-out;
        transition:         background-color 200ms ease-in-out;

        position:           relative;

        display:            -webkit-box;

        display:            -webkit-flex;

        display:            -ms-flexbox;

        display:            flex;
        -webkit-box-orient:     horizontal;
        -webkit-box-direction:     normal;
        -webkit-flex-direction:     row;
            -ms-flex-direction:     row;
                flex-direction:     row;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
    }

.module__default .module__header:after
        {
            content:            '';
            position:           absolute;
            bottom:             0;
            left:               32px;
            display:            inline-block;
            width:              50px;
            height:             3px;
            background-color:   #4b627d;
        }

.module__default .module__content
    {
        padding-top:    40px;
        padding-bottom: 40px;
    }

.module__default .module__title
    {
        -webkit-box-flex:       0;
        -webkit-flex:       0 0 100%;
            -ms-flex:       0 0 100%;
                flex:       0 0 100%;
        max-width:  100%;
    }

.module__default .module__controls
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        position:           absolute;

        height:             24px;
        top:                calc(50% - 12px);

        right:              24px;

        -webkit-box-orient:     horizontal;

        -webkit-box-direction:     normal;

        -webkit-flex-direction:     row;

            -ms-flex-direction:     row;

                flex-direction:     row;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
        -webkit-box-pack:    end;
        -webkit-justify-content:    flex-end;
            -ms-flex-pack:    end;
                justify-content:    flex-end;
    }

.module__default .module__controls > *
        {
            -webkit-box-flex:           0;
            -webkit-flex:           0 0 auto;
                -ms-flex:           0 0 auto;
                    flex:           0 0 auto;

            margin-right:   16px;
            margin-bottom:  0;
        }

.module__default .module__controls > *:last-child
            {
                margin-right:   0;
            }

.module__default .module__loadingOverlay
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-orient:     horizontal;
        -webkit-box-direction:     normal;
        -webkit-flex-direction:     row;
            -ms-flex-direction:     row;
                flex-direction:     row;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;

        position:           absolute;
        z-index:            9999999;

        top:                0;
        bottom:             0;
        left:               0;
        right:              0;

        width:              100%;
        height:             100%;

        background:         rgba(255, 255, 255, 0.5);
    }

.module__level__3 > .module__header, .module__level__3 > .module__content, .module__level__4 > .module__header, .module__level__4 > .module__content
    {
        padding-top:    16px;
        padding-bottom: 16px;
    }

.module__collapsible > .module__header
    {
        cursor:         pointer;
        padding-bottom: 24px;
    }

.module__collapsible > .module__header:after
        {
            display:    none;
        }

.module__default .module__collapsible:not(.module__collapsed) > .module__header, .module__default .module__collapsible.module__collapsed > .module__header:hover
        {
            background: #f6f7f9;
        }

.module__collapsible.module__level__3 > .module__header, .module__collapsible.module__level__4 > .module__header
        {
            padding-bottom: 16px;
        }

.module__collapsed > .module__content
    {
        display:    none;
    }

.module__moduleError
{
    border-left: 5px solid #ff5460;
}

.module__moduleError > .module__header, .module__moduleError > .module__content
    {
        padding-left: 19px;
    }
