.tooltip__default
{
    position:   relative;
    display:    inline-block;
    max-width:  100%;
}

.tooltip__default .tooltip__content
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
            -ms-flex-direction: column;
                flex-direction: column;
    }

.tooltip__default .tooltip__tooltipContainer
    {
        visibility:     hidden;
        position:       absolute;
        width:          360px;
        line-height:    0;
        z-index:        99999;
    }

.tooltip__default .tooltip__tooltip
    {
        visibility:     visible;
        position:       relative;
        display:        inline-block;

        -webkit-box-sizing:     border-box;

                box-sizing:     border-box;
        min-width:      var(--tooltipMinSize);
        min-height:     var(--tooltipMinSize);
        max-width:      360px;

        border:         1px solid;
        border-radius:  4px;
        -webkit-box-shadow:     0 2px 8px  rgba(89, 116, 148, 0.5);
                box-shadow:     0 2px 8px  rgba(89, 116, 148, 0.5);

        padding:        16px;

        text-align:     left;
    }

.tooltip__default .tooltip__tooltip::before /* hover area */
        {
            content:    "";
            position:   absolute;
            display:    block;
        }

.tooltip__default .tooltip__tooltip::after /* caret */
        {
            content:                "";
            display:                block;
            position:               absolute;

            -webkit-box-sizing:             inherit;

                    box-sizing:             inherit;
            width:                  10px;
            height:                 10px;

            border:                 inherit;
            border-top-color:       transparent;
            border-left-color:      transparent;

            background:             inherit;
        }

.tooltip__default .tooltip__message
    {
        min-height:     24px;
        min-width:      24px;

        line-height:    24px;
        color:          #ffffff;
        word-wrap:  break-word;
        text-transform: none;
    }

.tooltip__default .tooltip__message a:link,.tooltip__default .tooltip__message a:visited
        {
            color:              inherit !important;
            text-decoration:    underline;
        }

.tooltip__default .tooltip__message a:-webkit-any-link
        {
            color:              inherit !important;
            text-decoration:    underline;
        }

.tooltip__default .tooltip__message a:-moz-any-link
        {
            color:              inherit !important;
            text-decoration:    underline;
        }

.tooltip__default .tooltip__message a:any-link
        {
            color:              inherit !important;
            text-decoration:    underline;
        }

.tooltip__default .tooltip__close
    {
        position:   absolute;
        top:        16px;
        right:      16px;
    }

.tooltip__dismissible .tooltip__tooltip
    {
        padding-right:  48px;
    }

.tooltip__position__top .tooltip__tooltip::before, .tooltip__position__topLeft .tooltip__tooltip::before, .tooltip__position__topRight .tooltip__tooltip::before, .tooltip__position__bottom .tooltip__tooltip::before, .tooltip__position__bottomLeft .tooltip__tooltip::before, .tooltip__position__bottomRight .tooltip__tooltip::before
        {
