            right:  0;
            height: 16px;
        }

.tooltip__position__top .tooltip__tooltipContainer, .tooltip__position__topLeft .tooltip__tooltipContainer, .tooltip__position__topRight .tooltip__tooltipContainer
    {
        bottom: calc(100% + 16px);
    }

.tooltip__position__top .tooltip__tooltip::before, .tooltip__position__top .tooltip__tooltip::after, .tooltip__position__topLeft .tooltip__tooltip::before, .tooltip__position__topLeft .tooltip__tooltip::after, .tooltip__position__topRight .tooltip__tooltip::before, .tooltip__position__topRight .tooltip__tooltip::after
        {
            top:    calc(100% + 1px);
        }

.tooltip__position__top .tooltip__tooltip::after, .tooltip__position__topLeft .tooltip__tooltip::after, .tooltip__position__topRight .tooltip__tooltip::after
        {
            -webkit-transform:  translateX( -50% ) translateY( -50% ) rotate( 45deg );
                    transform:  translateX( -50% ) translateY( -50% ) rotate( 45deg );
        }

.tooltip__position__bottom .tooltip__tooltipContainer, .tooltip__position__bottomLeft .tooltip__tooltipContainer, .tooltip__position__bottomRight .tooltip__tooltipContainer
    {
        top:    calc(100% + 16px);
    }

.tooltip__position__bottom .tooltip__tooltip::before, .tooltip__position__bottom .tooltip__tooltip::after, .tooltip__position__bottomLeft .tooltip__tooltip::before, .tooltip__position__bottomLeft .tooltip__tooltip::after, .tooltip__position__bottomRight .tooltip__tooltip::before, .tooltip__position__bottomRight .tooltip__tooltip::after
