.tooltip__position__bottom .tooltip__tooltip::after, .tooltip__position__bottomLeft .tooltip__tooltip::after, .tooltip__position__bottomRight .tooltip__tooltip::after
        {
            -webkit-transform:  translateX( -50% ) translateY( 50% ) rotate( 225deg );
                    transform:  translateX( -50% ) translateY( 50% ) rotate( 225deg );
        }

.tooltip__position__top .tooltip__tooltipContainer, .tooltip__position__bottom .tooltip__tooltipContainer
    {
        left:       50%;
        text-align: center;
        -webkit-transform:  translateX( -50% );
