        text-align: right;
    }

.tooltip__position__topLeft .tooltip__tooltip::after, .tooltip__position__bottomLeft .tooltip__tooltip::after
        {
            left:  calc(100% - 28px);
        }

.tooltip__position__topRight .tooltip__tooltipContainer, .tooltip__position__bottomRight .tooltip__tooltipContainer
    {
        left:       calc(50% - 28px);
        text-align: left;
    }

.tooltip__position__topRight .tooltip__tooltip::after, .tooltip__position__bottomRight .tooltip__tooltip::after
        {
            left:   28px;
        }

.tooltip__position__left .tooltip__tooltip::before, .tooltip__position__leftTop .tooltip__tooltip::before, .tooltip__position__leftBottom .tooltip__tooltip::before, .tooltip__position__right
.tooltip__position__rightTop .tooltip__tooltip::before, .tooltip__position__rightBottom .tooltip__tooltip::before
    {
        top:    0;
        bottom: 0;
        width:  16px;
    }

.tooltip__position__left .tooltip__tooltipContainer, .tooltip__position__leftTop .tooltip__tooltipContainer, .tooltip__position__leftBottom .tooltip__tooltipContainer
    {
        right:      calc(100% + 16px);
        text-align: right;
    }

.tooltip__position__left .tooltip__tooltip::before, .tooltip__position__left .tooltip__tooltip::after, .tooltip__position__leftTop .tooltip__tooltip::before, .tooltip__position__leftTop .tooltip__tooltip::after, .tooltip__position__leftBottom .tooltip__tooltip::before, .tooltip__position__leftBottom .tooltip__tooltip::after
        {
