        }

.tooltip__position__right .tooltip__tooltipContainer, .tooltip__position__rightTop .tooltip__tooltipContainer, .tooltip__position__rightBottom .tooltip__tooltipContainer
    {
        left:       calc(100% + 16px);
        text-align: left;
    }

.tooltip__position__right .tooltip__tooltip::before, .tooltip__position__right .tooltip__tooltip::after, .tooltip__position__rightTop .tooltip__tooltip::before, .tooltip__position__rightTop .tooltip__tooltip::after, .tooltip__position__rightBottom .tooltip__tooltip::before, .tooltip__position__rightBottom .tooltip__tooltip::after
        {
