        }

.tooltip__position__left .tooltip__tooltipContainer, .tooltip__position__right .tooltip__tooltipContainer
    {
        top:        50%;
        -webkit-transform:  translateY( -50% );
                transform:  translateY( -50% );
    }

.tooltip__position__left .tooltip__tooltip::after, .tooltip__position__right .tooltip__tooltip::after
        {
            top:   50%;
        }

.tooltip__position__left .tooltip__tooltip::after, .tooltip__position__leftTop .tooltip__tooltip::after, .tooltip__position__leftBottom .tooltip__tooltip::after
