.iconButton__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.iconButton__default:last-child
    {
        margin-bottom:  0;
    }

:root
{ /* Math.sqrt( 2 ) */
}

.iconButton__default
{
    -webkit-appearance:         none !important;
       -moz-appearance:         none !important;
            appearance:         none !important;

    display:            -webkit-box;

    display:            -webkit-flex;

    display:            -ms-flexbox;

    display:            flex;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;

    width:              auto;

    margin:             0;
    padding:            0;
    border:             none;

    background:         transparent;
    cursor:             pointer;
}

.iconButton__default .iconButton__icon
    {
        -webkit-box-flex:   0;
        -webkit-flex:   none;
            -ms-flex:   none;
                flex:   none;
    }

.iconButton__default:focus
    {
        outline:    none;
    }

.iconButton__default:focus::after
        {
            content:        '';
            display:        block;

            position:       absolute;
            top:            50%;
            left:           50%;
            -webkit-transform:      translateX( -50% ) translateY( -50% );
                    transform:      translateX( -50% ) translateY( -50% );

            width:          141.42136%;
            height:         141.42136%;

            border:         1px solid transparent;
            border-radius:  50%;

            border-color:   #19a2e2;
            -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                    box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
        }

.iconButton__disabled
{
    cursor:         default;
    pointer-events: none;
    opacity:        0.36;
}

.iconButton__background
{
    border:         1px solid transparent;
    border-radius:  50%;
    padding:        7px;

    background:     #ffffff;
    opacity:        0.9;
}

.iconButton__background:focus
    {
        border-color:   #19a2e2;
        -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.iconButton__background:focus::after
        {
            display:    none;
        }
