.messageBox__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.messageBox__default:last-child
    {
        margin-bottom:  0;
    }

.messageBox__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;
    -webkit-align-content:      center;
        -ms-flex-line-pack:      center;
            align-content:      center;

    padding:            16px;

    border-radius:      2px;
}

.messageBox__type__default
{
    /* nothing to do here */
}

.messageBox__type__info .messageBox__text, .messageBox__type__error .messageBox__text, .messageBox__type__alert .messageBox__text, .messageBox__type__success .messageBox__text
    {
        color: #ffffff;
    }

.messageBox__type__info
{
    background: #69829e;
}

.messageBox__type__error
{
    background: #ff5460;
}

.messageBox__type__alert
{
    background: #ff965a;
}

.messageBox__type__success
{
    background: #1bbab0;
}
