.spinner__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.spinner__default:last-child
    {
        margin-bottom:  0;
    }

.spinner__default, .spinner__size__big
{
    position:           relative;
    background-color:   transparent;
    border-style:       solid;
    border-color:       rgba(66, 86, 110, 0.35);
    border-radius:      50%;
    -webkit-box-sizing:         content-box;
            box-sizing:         content-box;
    display:            inline-block;
}

.spinner__default:after, .spinner__size__big:after
    {
        position:       absolute;
        content:        "";
        top:            0;
        left:           0;
        border-style:   solid;
        border-color:   #42566e transparent transparent transparent;
        border-radius:  50%;
        -webkit-box-sizing:     border-box;
                box-sizing:     border-box;
    }

.spinner__default
{
    width:                10px;
    height:               10px;
    min-height:           10px;
    border-width:         3px;
}

.spinner__default:after
    {
        width:              10px;
        height:             10px;
        border-width:       1px;
        -webkit-animation:          spinner__defaultSpinnerRingRotate .8s infinite ease-in-out;
                animation:          spinner__defaultSpinnerRingRotate .8s infinite ease-in-out;
    }

@-webkit-keyframes spinner__defaultSpinnerRingRotate
{
    0%
    {
        -webkit-transform: rotateZ(0deg) scale(1.48);
                transform: rotateZ(0deg) scale(1.48);
    }
    100%
    {
        -webkit-transform: rotateZ(360deg) scale(1.48);
                transform: rotateZ(360deg) scale(1.48);
    }
}

@keyframes spinner__defaultSpinnerRingRotate
{
    0%
    {
        -webkit-transform: rotateZ(0deg) scale(1.48);
                transform: rotateZ(0deg) scale(1.48);
    }
    100%
    {
        -webkit-transform: rotateZ(360deg) scale(1.48);
                transform: rotateZ(360deg) scale(1.48);
    }
}

/* big */

.spinner__size__big
{
    width:                    30px;
    height:                   30px;
    min-height:               30px;
    border-width:             5px;
}

.spinner__size__big:after
    {
        width:                  30px;
        height:                 30px;
        border-width:           3px;
        -webkit-animation:              spinner__bigSpinnerRingRotate .8s infinite ease-in-out;
                animation:              spinner__bigSpinnerRingRotate .8s infinite ease-in-out;
    }

@-webkit-keyframes spinner__bigSpinnerRingRotate
{
    0%
    {
        -webkit-transform: rotateZ(0deg) scale(1.3);
                transform: rotateZ(0deg) scale(1.3);
    }
    100%
    {
        -webkit-transform: rotateZ(360deg) scale(1.3);
                transform: rotateZ(360deg) scale(1.3);
    }
}

@keyframes spinner__bigSpinnerRingRotate
{
    0%
    {
        -webkit-transform: rotateZ(0deg) scale(1.3);
                transform: rotateZ(0deg) scale(1.3);
    }
    100%
    {
        -webkit-transform: rotateZ(360deg) scale(1.3);
                transform: rotateZ(360deg) scale(1.3);
    }
}
