.tabButton__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.tabButton__default:last-child
    {
        margin-bottom:  0;
    }

.tabButton__default
{
    -webkit-appearance:     none !important;
       -moz-appearance:     none !important;
            appearance:     none !important;

    position:       relative;
    display:        table; /* flex doesnt work here */

    width:          auto;
    height:         auto;

    padding:        0;

    border:         none;
    border-radius:  2px;

    background:     transparent;
    cursor:         pointer;
}

.tabButton__default .tabButton__content
    {
        /* Firefox doesnt properly support flex directly
         * on button so we need to set it here
         */
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
        -webkit-box-sizing:         border-box;
                box-sizing:         border-box;
        height:             40px;
        padding:            0 16px;
        text-align:         center;
    }

.tabButton__default .tabButton__label
    {
        font-size:      14px;
        line-height:    16px;
        letter-spacing: 0.5px;

        overflow:       hidden;
        text-overflow:  ellipsis;
        white-space:    nowrap;
    }

.tabButton__default .tabButton__iconContainer
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
    }

.tabButton__default .tabButton__loadingOverlay
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-orient:     horizontal;
        -webkit-box-direction:     normal;
        -webkit-flex-direction:     row;
            -ms-flex-direction:     row;
                flex-direction:     row;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;

        position:           absolute;

        top:                0;
        bottom:             0;

        left:               0;
        right:              0;

        width:              100%;
        height:             100%;
    }

.tabButton__default .tabButton__loadingOverlay .tabButton__spinner
        {
            border-color:   rgba(255, 255, 255, 0.5);
        }

.tabButton__default .tabButton__loadingOverlay .tabButton__spinner::after
            {
                border-top-color:   #ffffff;
            }

.tabButton__iconPosition__right .tabButton__content
    {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
        -webkit-flex-direction: row-reverse;
            -ms-flex-direction: row-reverse;
                flex-direction: row-reverse;
    }

.tabButton__iconPosition__right .tabButton__iconContainer
    {
        margin-left:    4px; /* space between icon and text */
        margin-right:   -8px; /* half of content padding */
    }

.tabButton__iconPosition__left .tabButton__iconContainer
    {
        margin-right:   4px; /* space between icon and text */
        margin-left:    -8px; /* half of content padding */
    }

.tabButton__role__default
{
    background: #19a2e2;
    color:      #ffffff;
}

.tabButton__role__default.tabButton__fakeHovered, .tabButton__role__default:hover
    {
        background: #30abe4;
        color:      #ffffff;
    }

.tabButton__role__default:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.tabButton__role__secondary
{
    background: #eef1f4;
    color:      #8296ae;
}

.tabButton__role__secondary.tabButton__fakeHovered, .tabButton__role__secondary:hover
    {
        background: #cfd7e0;
        color:      #8296ae;
    }

.tabButton__role__secondary:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(89, 116, 148, 0.3);
                box-shadow: 0 0 0 2px rgba(89, 116, 148, 0.3);
    }

.tabButton__role__secondary .tabButton__icon
    {
        fill:   #8296ae;
        color:  #8296ae;
    }

.tabButton__role__secondary .tabButton__loadingOverlay .tabButton__spinner
        {
            border-color:   rgba(130, 150, 174, 0.25);
        }

.tabButton__role__secondary .tabButton__loadingOverlay .tabButton__spinner::after
            {
                border-top-color:   #8296ae;
            }

.tabButton__role__promoted
{
    background: #1bbab0;
    color:      #ffffff;
}

.tabButton__role__promoted.tabButton__fakeHovered, .tabButton__role__promoted:hover
    {
        background: #32c1b8;
        color:      #ffffff;
    }

.tabButton__role__promoted:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(27, 186, 176, 0.3);
                box-shadow: 0 0 0 2px rgba(27, 186, 176, 0.3);
    }

.tabButton__role__critical
{
    background: #ff5460;
    color:      #ffffff;
}

.tabButton__role__critical.tabButton__fakeHovered, .tabButton__role__critical:hover
    {
        background: #ff6d77;
        color:      #ffffff;
    }

.tabButton__role__critical:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.tabButton__role__subtle
{
    height:         auto;
    background:     transparent;
    border-radius:  20px;
}

.tabButton__role__subtle .tabButton__content
    {
        height:         32px;
        border-radius:  20px;
        border:         1px solid #19a2e2;

        -webkit-box-sizing:     border-box;

                box-sizing:     border-box;

        color:          #19a2e2;
    }

.tabButton__role__subtle .tabButton__icon
    {
        fill:   #19a2e2;
        color:  #19a2e2;
    }

.tabButton__role__subtle .tabButton__loadingOverlay .tabButton__spinner
        {
            border-color:   rgba(25, 162, 226, 0.25);
        }

.tabButton__role__subtle .tabButton__loadingOverlay .tabButton__spinner::after
            {
                border-top-color:   #19a2e2;
            }

.tabButton__role__subtle.tabButton__fakeHovered .tabButton__content, .tabButton__role__subtle:hover .tabButton__content
        {
            background-color:   #19a2e2;
            color:              #ffffff;
        }

.tabButton__role__subtle.tabButton__fakeHovered .tabButton__icon, .tabButton__role__subtle:hover .tabButton__icon
        {
            fill:   #ffffff;
        }

.tabButton__role__subtle:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.tabButton__role__control
{
    height: auto;
    color:  #4b627d;
}

.tabButton__role__control .tabButton__content
    {
        height:         32px;
        border:         1px solid transparent;
        border-radius:  2px;
        padding: 0 8px;
    }

.tabButton__iconPosition__left.tabButton__role__control .tabButton__iconContainer
        {
            margin-left:    -4px;
        }

.tabButton__iconPosition__right.tabButton__role__control .tabButton__iconContainer
        {
            margin-right:   -4px;
        }

.tabButton__role__control .tabButton__icon
    {
        fill:   #4b627d;
        color:  #4b627d;
    }

.tabButton__role__control.tabButton__fakeHovered, .tabButton__role__control:hover
    {
        color:  #19a2e2;
    }

.tabButton__role__control.tabButton__fakeHovered .tabButton__icon, .tabButton__role__control:hover .tabButton__icon
        {
            fill:   #19a2e2;
            color:  #19a2e2;
        }

.tabButton__role__control .tabButton__loadingOverlay .tabButton__spinner
        {
            border-color:   rgba(75, 98, 125, 0.25);
        }

.tabButton__role__control .tabButton__loadingOverlay .tabButton__spinner::after
            {
                border-top-color:   #4b627d;
            }

.tabButton__role__control:focus
    {
        outline:    none;
    }

.tabButton__role__control:focus .tabButton__content
        {
            border-color:   #19a2e2;
            -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                    box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
        }

.tabButton__loading, .tabButton__disabled
{
    cursor:         default;
    pointer-events: none;
}

.tabButton__loading .tabButton__label, .tabButton__loading .tabButton__iconContainer
    {
        opacity:    0;
    }

.tabButton__disabled.tabButton__role__default
    {
        background: #bae3f6;
        color:      #52b9e9;
    }

.tabButton__disabled.tabButton__role__default .tabButton__icon
        {
            fill:   rgb(83, 185, 233);
            color:  rgb(83, 185, 233);
        }

.tabButton__disabled.tabButton__role__secondary
    {
        color:      #afbccb;
        background: transparent;
    }

.tabButton__disabled.tabButton__role__secondary .tabButton__icon
        {
            fill:   #afbccb;
            color:  #afbccb;
        }

.tabButton__disabled.tabButton__role__promoted
    {
        background: #bbeae7;
        color:      #5fcec7;
    }

.tabButton__disabled.tabButton__role__promoted .tabButton__icon
        {
            fill:   rgb(84, 203, 196);
            color:  rgb(84, 203, 196);
        }

.tabButton__disabled.tabButton__role__critical
    {
        background: #ffcccf;
        color:      #ff8790;
    }

.tabButton__disabled.tabButton__role__critical .tabButton__icon
        {
            fill:   rgb(255, 127, 136);
            color:  rgb(255, 127, 136);
        }

.tabButton__disabled.tabButton__role__subtle .tabButton__content
        {
            color:          #75c7ed;
            background:     #ffffff;
            border-color:   #75c7ed;
        }

.tabButton__disabled.tabButton__role__subtle .tabButton__icon
        {
            fill:   #75c7ed;
            color:  #75c7ed;
        }

.tabButton__disabled.tabButton__role__control .tabButton__content
        {
            color:  #98a8bc;
        }

.tabButton__disabled.tabButton__role__control .tabButton__icon
        {
            fill:   #98a8bc;
            color:  #98a8bc;
        }

/* imports base.css */

.tabButton__default
{
    color:          #c7d0db;
    text-transform: none;
}

.tabButton__default .tabButton__content
    {
        min-width:      0;
        margin:         0;
        padding:        0;

        border:         1px solid transparent;
        border-radius:  0 0 2px;
    }

.tabButton__default .tabButton__label
    {

        font-size:      14px;
        line-height:    16px;
        padding-left:   8px;
        padding-right:  8px;
        letter-spacing: 0;
        margin-bottom:  -1px;
    }

.tabButton__default .tabButton__subtitle
    {
        font-weight: 	normal;
        text-transform: none;
        text-transform: initial;
    }

.tabButton__default:focus
    {
        outline:    none;
    }

.tabButton__default:focus .tabButton__content
        {
            border:     1px solid #19a2e2;
            -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                    box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
        }

.tabButton__active
{
    color:          #19a2e2;
    margin-bottom:  0;
    pointer-events: none;
    cursor:         default;
}

.tabButton__active .tabButton__content
    {
        border-bottom-color:   #19a2e2;
    }
