.timeInput__default
{
    display:            inline-block;
    position:           relative;

    font-size:          10px;
    line-height:        16px;
    text-align:         right;

    vertical-align:     bottom;

    width:              50%;

    -webkit-box-sizing:         border-box;

            box-sizing:         border-box;
}

.timeInput__default:hover, .timeInput__default.timeInput__fakeHovered
    {
        border-color:   #19a2e2;
    }

.timeInput__default .timeInput__hour, .timeInput__default .timeInput__min
    {
        -webkit-box-sizing:         border-box;
                box-sizing:         border-box;
        width:              36px;
        height:             24px;
        padding:            0;

        border:             none;
        border-radius:      2px;

        text-align:         center;
        font:               inherit;
        line-height:        16px;
        font-size:          13px;
        color:              #ffffff;

        background-color:   #1799d6;
    }

.timeInput__default .timeInput__hour:focus, .timeInput__default .timeInput__min:focus
        {
            -webkit-box-shadow:     0 0 0 2px rgba(255, 255, 255, 0.3);
                    box-shadow:     0 0 0 2px rgba(255, 255, 255, 0.3);
            outline:        none;
        }

.timeInput__default .timeInput__hour::-webkit-input-placeholder, .timeInput__default .timeInput__min::-webkit-input-placeholder
        {
            line-height:    16px;
            font-size:      13px;
            font-weight:    300;
            color:          #ffffff;
        }

.timeInput__default .timeInput__hour:-ms-input-placeholder, .timeInput__default .timeInput__min:-ms-input-placeholder
        {
            line-height:    16px;
            font-size:      13px;
            font-weight:    300;
            color:          #ffffff;
        }

.timeInput__default .timeInput__hour::-ms-input-placeholder, .timeInput__default .timeInput__min::-ms-input-placeholder
        {
            line-height:    16px;
            font-size:      13px;
            font-weight:    300;
            color:          #ffffff;
        }

.timeInput__default .timeInput__hour::placeholder, .timeInput__default .timeInput__min::placeholder
        {
            line-height:    16px;
            font-size:      13px;
            font-weight:    300;
            color:          #ffffff;
        }

.timeInput__default span
    {
        -webkit-user-select:        none;
           -moz-user-select:        none;
            -ms-user-select:        none;
                user-select:        none;
        line-height:        16px;
        font-size:          13px;
        color:              #ffffff;
        margin:             0 3px;
        display:            inline-block;
    }
