.tagInput__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.tagInput__default:last-child
    {
        margin-bottom:  0;
    }

.tagInput__default .tagInput__container
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-flex-wrap:          wrap;
            -ms-flex-wrap:          wrap;
                flex-wrap:          wrap;
        -webkit-box-align:        start;
        -webkit-align-items:        flex-start;
            -ms-flex-align:        start;
                align-items:        flex-start;
        -webkit-align-content:      flex-start;
            -ms-flex-line-pack:      start;
                align-content:      flex-start;

        -webkit-box-sizing:         border-box;

                box-sizing:         border-box;
        width:              100%;

        border:             1px solid #c7d0db;
        border-radius:      2px;

        padding-top:        3px;
        padding-right:      3px;
        padding-bottom:     0;
        padding-left:       3px;

        background-color:   #ffffff;
        outline:            none;

        overflow-x:         hidden;
    }

.tagInput__default .tagInput__container > *
        {
            margin-right:   4px;
            margin-bottom:  3px;
        }

.tagInput__default .tagInput__input
    {
        -webkit-box-flex:           1;
        -webkit-flex:           1 0 0%;
            -ms-flex:           1 0 0%;
                flex:           1 0 0%;

        -webkit-box-sizing:     border-box;

                box-sizing:     border-box;
        min-width:      5ch;
        min-height:     32px;

        padding:        0;
        padding-top:    1px;

        border:         none;
        margin-right:   0;

        background:     transparent;
        font-size:      14px;
        line-height:    24px;
        letter-spacing: 0.4px;
        text-indent:    5px;
        color:          #546e8c;

        resize:         none;
    }

/* required for Safari */

.tagInput__default .tagInput__input:focus
        {
            outline:    none;
        }

.tagInput__default .tagInput__input:focus::-webkit-input-placeholder
            {
                color: transparent !important;
            }

.tagInput__default .tagInput__input:focus:-ms-input-placeholder
            {
                color: transparent !important;
            }

.tagInput__default .tagInput__input:focus::-ms-input-placeholder
            {
                color: transparent !important;
            }

.tagInput__default .tagInput__input:focus::placeholder
            {
                color: transparent !important;
            }

.tagInput__default .tagInput__input::-webkit-input-placeholder
        {
            color:         #d7dde5;
        }

.tagInput__default .tagInput__input:-ms-input-placeholder
        {
            color:         #d7dde5;
        }

.tagInput__default .tagInput__input::-ms-input-placeholder
        {
            color:         #d7dde5;
        }

.tagInput__default .tagInput__input::placeholder
        {
            color:         #d7dde5;
        }

.tagInput__fakeHovered .tagInput__container
    {
        border-color:   #19a2e2;
        -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.tagInput__disabled .tagInput__container
    {
        pointer-events: none;
    }

.tagInput__disabled .tagInput__container:hover
        {
            border-color:   ( --PC-GREY--L70 );
        }

.tagInput__error .tagInput__container
    {
        border-color:   #ff5460 !important; /* yes, always override other states */
        -webkit-box-shadow:     0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow:     0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.tagInput__resizable .tagInput__container
    {
        resize:        vertical;
    }
