.page__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.page__default:last-child
    {
        margin-bottom:  0;
    }

.page__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-orient:     vertical;
    -webkit-box-direction:     normal;
    -webkit-flex-direction:     column;
        -ms-flex-direction:     column;
            flex-direction:     column;
    -webkit-box-pack:    start;
    -webkit-justify-content:    flex-start;
        -ms-flex-pack:    start;
            justify-content:    flex-start;
    -webkit-box-align:        start;
    -webkit-align-items:        flex-start;
        -ms-flex-align:        start;
            align-items:        flex-start;

    width:              100%;
    height:             auto;

    min-width:          1024px;
    min-height:         100vh;

    margin:             0;

    background:         #f6f7f9;
}

.page__overflow__hidden
{
    overflow: hidden;
}

.page__overflow__visible
{
    overflow: visible;
}

.page__overflow__scroll
{
    overflow: scroll;
}

.page__overflow__scrollX
{
    overflow-x: scroll;
}

.page__overflow__scrollY
{
    overflow-y: scroll;
}

.overflow__hidden
    {
        overflow:     hidden;
        max-height:   100vh;
        max-width:    100vw;
    }

.overflow__visible
    {
        overflow: visible;
    }

.overflow__scroll
    {
        overflow: scroll;
        max-height:   100vh;
        max-width:    100vw;
    }

.overflow__scrollX
    {
        overflow-x: scroll;
        max-width:    100vw;
    }

.overflow__scrollY
    {
        overflow-y: scroll;
        max-height:   100vh;
    }
