.switch__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.switch__default:last-child
    {
        margin-bottom:  0;
    }

.switch__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;


    width:              48px;
    height:             40px;
}

.switch__default .switch__input
    {
        position:   absolute;
        left:       -9999px;
    }

.switch__default .switch__input:focus + .switch__label
            {
                outline:        none;

                border:         1px solid #19a2e2;
                -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                        box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
            }

.switch__default .switch__input:focus + .switch__label::before
                {
                    border-color:   #19a2e2;
                    -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                            box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                }

.switch__default .switch__label
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;

        width:          40px;
        height:         16px;

        border-radius:  8px;

        background:     #69829e;

        cursor:         pointer;
        -webkit-transition:     background-color 0.2s ease-in-out;
        transition:     background-color 0.2s ease-in-out;
    }

.switch__default .switch__label::before
        {
            content:        '';

            display:        block;
            position:       absolute;

            -webkit-box-sizing:     border-box;

                    box-sizing:     border-box;

            top:            50%;
            left:           0;
            -webkit-transform:      translateY( -50% );
                    transform:      translateY( -50% );

            width:          25px;
            height:         25px;

            border-radius:  100%;
            border-style:   solid;
            border-width:   2px;
            border-color:   #597494;

            background:     #ffffff;

            -webkit-transition:     background-color 0.2s ease-in-out, left 0.25s cubic-bezier( 0.215, 0.61, 0.355, 1 );

            transition:     background-color 0.2s ease-in-out, left 0.25s cubic-bezier( 0.215, 0.61, 0.355, 1 );
        }

.switch__default .switch__input:checked + .switch__label
    {
        background: #30abe4;
        -webkit-transition: background-color 0.2s ease-in-out;
        transition: background-color 0.2s ease-in-out;
    }

.switch__default .switch__input:checked + .switch__label::before
        {
            left:           calc(100% - 25px);
            border-color:   #19a2e2;
        }

.switch__disabled .switch__label
    {
        cursor:     default;
        background: #98a8bc;
    }

.switch__disabled .switch__label::before
        {
            border-color: #98a8bc;
        }

.switch__disabled .switch__input:checked + .switch__label
    {
        background: #97d5f1;
    }

.switch__disabled .switch__input:checked + .switch__label::before
        {
            border-color: #97d5f1;
        }

.switch__disabled :hover .switch__label, .switch__disabled .switch__fakeHovered .switch__label
        {
            background: #afbccb;
        }

.switch__disabled :hover .switch__label::before, .switch__disabled .switch__fakeHovered .switch__label::before
            {
                border-color: #98a8bc;
            }

.switch__disabled :hover input:checked + .switch__label, .switch__disabled .switch__fakeHovered input:checked + .switch__label
        {
            background: #97d5f1;
        }

.switch__disabled :hover input:checked + .switch__label::before, .switch__disabled .switch__fakeHovered input:checked + .switch__label::before
            {
                border-color: #75c7ed;
            }
