.icon__default
{
    display:                block;
    background-repeat:      no-repeat;
    background-position:    center;

    fill:                   #42566e;
    color:                  #42566e;
}

.icon__size__S
{
    width:                  24px;
    height:                 24px;
}

.icon__size__M
{
    width:                  32px;
    height:                 32px;
}

.icon__size__L
{
    width:                  40px;
    height:                 40px;
}

.icon__size__XL
{
    width:                  48px;
    height:                 48px;
}

/* fill:                       var( --GS-2 ); */

.icon__theme__control:hover, .icon__theme__control.icon__fakeHovered, button:focus > .icon__theme__control
    {
        fill:                   #19a2e2;
    }

.icon__theme__button
{
    fill:                       #ffffff;
    color:                      #ffffff;
}

.icon__theme__light, .icon__theme__dark, .icon__theme__navigation
{
    /* fill:                       color( var( --GS-2 ) a( 50% ) ); */
}

.icon__theme__light:hover, .icon__theme__light.icon__fakeHovered, button:focus > .icon__theme__light
    {
        fill:                   #19a2e2;
    }

.icon__theme__dark:hover, .icon__theme__dark.icon__fakeHovered, button:focus > .icon__theme__dark
    {
        fill:                   #ffffff;
    }

.icon__theme__navigation:hover, .icon__theme__navigation.icon__fakeHovered, button:focus > .icon__theme__navigation
    {
        /* fill:                   var( --GS-2 ); */
    }

:not(.icon__theme__button).icon__type__alert
    {
        color:                      #ff965a;
        fill:                       #ff965a;
    }

:not(.icon__theme__button).icon__type__approved, :not(.icon__theme__button).icon__type__validation
    {
        color:                      #1bbab0;
        fill:                       #1bbab0;
    }

:not(.icon__theme__button).icon__type__declined, :not(.icon__theme__button).icon__type__error
    {
        color:                      #ff5460;
        fill:                       #ff5460;
    }

:not(.icon__theme__button).icon__type__ended
    {
        color:                      #69829e;
        fill:                       #69829e;
    }

:not(.icon__theme__button).icon__type__running, :not(.icon__theme__button).icon__type__info
    {
        color:                      #19a2e2;
        fill:                       #19a2e2;
    }

:not(.icon__theme__button).icon__type__pending
    {
        color:                      #ff965a;
        fill:                       #ff965a;
    }

.icon__disabled:not(.icon__theme__button)
{
    pointer-events:                 none;
    fill:                           #c7d0db;
    color:                          #c7d0db;
}

