.notificationBar__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.notificationBar__default:last-child
    {
        margin-bottom:  0;
    }

.notificationBar__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;

    padding:            16px;

    background:         #19a2e2;
}

.notificationBar__default .notificationBar__message, .notificationBar__default .notificationBar__info, .notificationBar__default .notificationBar__close
    {
        margin-bottom:  0;
    }

.notificationBar__default .notificationBar__info
    {
        margin-right:   20px;
        fill:           #42566e;
    }

.notificationBar__default .notificationBar__close
    {
        position:   absolute;
        right:      16px;
    }

.notificationBar__top
{
    position:   fixed;
    top:        0;
    left:       0;
    z-index:    1000;
}

.notificationBar__type__error
{
    background: #ff5460;
}

.notificationBar__type__alert
{
    background: #ffd46b;
}

.notificationBar__type__success
{
    background: #1bbab0;
}

.notificationBar__message
{
    color: #ffffff;
}
