.navList__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.navList__default:last-child
    {
        margin-bottom:  0;
    }

.navList__default
{
    display:    -webkit-box;
    display:    -webkit-flex;
    display:    -ms-flexbox;
    display:    flex;
    padding:    0;
    margin:     0;

    width:      auto;

    list-style: none;
}

.navList__layout__horizontal
{
    -webkit-box-orient:     horizontal;
    -webkit-box-direction:     normal;
    -webkit-flex-direction:     row;
        -ms-flex-direction:     row;
            flex-direction:     row;
    -webkit-box-pack:    start;
    -webkit-justify-content:    flex-start;
        -ms-flex-pack:    start;
            justify-content:    flex-start;
    -webkit-box-align:        start;
    -webkit-align-items:        flex-start;
        -ms-flex-align:        start;
            align-items:        flex-start;
}

.navList__layout__horizontal > *
    {
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
        margin-bottom:  0;
    }

.navList__layout__horizontal > *:last-child
        {
            margin-right:   0;
        }

.navList__layout__vertical
{
    -webkit-box-orient:     vertical;
    -webkit-box-direction:     normal;
    -webkit-flex-direction:     column;
        -ms-flex-direction:     column;
            flex-direction:     column;
    -webkit-box-pack:    start;
    -webkit-justify-content:    flex-start;
        -ms-flex-pack:    start;
            justify-content:    flex-start;
    -webkit-box-align:        stretch;
    -webkit-align-items:        stretch;
        -ms-flex-align:        stretch;
            align-items:        stretch;
}
