.paginator__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.paginator__default:last-child
    {
        margin-bottom:  0;
    }

.paginator__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-orient:     horizontal;
    -webkit-box-direction:     normal;
    -webkit-flex-direction:     row;
        -ms-flex-direction:     row;
            flex-direction:     row;
    -webkit-box-pack:    start;
    -webkit-justify-content:    flex-start;
        -ms-flex-pack:    start;
            justify-content:    flex-start;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;
}

.paginator__default > *
    {
        margin-bottom:  0;
    }

.paginator__arrows:not(:last-child), .paginator__ellipsis:not(:last-child), .paginator__pageButton:not(:last-child), .paginator__pageButtons:not(:last-child)
    {
        margin-right:       8px;
    }

.paginator__pageButton, .paginator__ellipsis
{
    border:             none;
    margin:             0;

    min-width:          24px;
    height:             24px;

    color:              #afbccb;

    font-size:          12px;
    line-height:        16px;
    letter-spacing:     0.2px;

    text-align:         center;
}

.paginator__pageButton:focus, .paginator__ellipsis:focus
    {
        outline:        none;
    }

.paginator__pageButton
{
    height:                 24px;
    background-color:       #eef1f4;

    padding-left:           8px;
    padding-right:          8px;
    padding-top:            0;
    padding-bottom:         0;

    border:                 1px solid #eef1f4;
    border-radius:          2px;
}

.paginator__pageButton:hover:not([disabled])
    {
        cursor:             pointer;
    }

.paginator__pageButton:hover, .paginator__pageButton:disabled
    {
        border-color:       #19a2e2;
        color:              #ffffff;
        background-color:   #19a2e2;
    }

.paginator__pageButton:focus
    {
        border-color:       #19a2e2;
        -webkit-box-shadow:         0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow:         0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.paginator__pageButtons
{
    display:            -webkit-inline-box;
    display:            -webkit-inline-flex;
    display:            -ms-inline-flexbox;
    display:            inline-flex;
    -webkit-box-align:        start;
    -webkit-align-items:        flex-start;
        -ms-flex-align:        start;
            align-items:        flex-start;

}

.paginator__pageButtons > *
    {
        margin-bottom:  0;
    }

.paginator__ellipsis
{
    padding:            0;
    height:             14px;
    background-color:   transparent;
}

.paginator__arrows
{
    border-radius:          2px;
}

.paginator__arrows:hover
    {
        background-color:   #eef1f4;
    }

.paginator__arrows:focus::after
    {
        top:    0;
        bottom: 0;
        left:   0;
        right:  0;
        border-radius:  2px;
    }
