.pageContent__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.pageContent__default:last-child
    {
        margin-bottom:  0;
    }

.pageContent__default
{
    -webkit-box-flex:   1;
    -webkit-flex:   1 0 auto;
        -ms-flex:   1 0 auto;
            flex:   1 0 auto;
    margin: 0;
}

.pageContent__default .pageContent__content
    {
        -webkit-box-sizing:    border-box;
                box-sizing:    border-box;

        margin:        0 auto;
        padding:       72px 0;
    }

.pageContent__default .pageContent__loadingOverlay
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-orient:     horizontal;
        -webkit-box-direction:     normal;
        -webkit-flex-direction:     row;
            -ms-flex-direction:     row;
                flex-direction:     row;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;

        position:           absolute;
        top:                0;
        bottom:             0;
        left:               0;
        right:              0;

        width:              100%;
        height:             100%;

        background:         rgba(255, 255, 255, 0.7);

        z-index:            999;
    }

.pageContent__default .pageContent__spinner
    {
        position:       fixed !important;
        top:            calc(50% - 20px);
        border-color:   rgba(25, 162, 226, 0.5) !important;
    }

.pageContent__default .pageContent__spinner::after
        {
            border-top-color:   #19a2e2 !important;
        }
