.card__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.card__default:last-child
    {
        margin-bottom:  0;
    }

.card__default
{
    display:          -webkit-box;
    display:          -webkit-flex;
    display:          -ms-flexbox;
    display:          flex;
    -webkit-box-orient:   vertical;
    -webkit-box-direction:   normal;
    -webkit-flex-direction:   column;
        -ms-flex-direction:   column;
            flex-direction:   column;
    -webkit-box-pack:  start;
    -webkit-justify-content:  flex-start;
        -ms-flex-pack:  start;
            justify-content:  flex-start;
    -webkit-box-align:      stretch;
    -webkit-align-items:      stretch;
        -ms-flex-align:      stretch;
            align-items:      stretch;

    background:       #ffffff;

    border-radius:    3px;

    border:           1px solid rgba(89, 116, 148, 0.12);
}

.card__alignX__left
{
    -webkit-box-align:    start;
    -webkit-align-items:    flex-start;
        -ms-flex-align:    start;
            align-items:    flex-start;
    text-align:     left;
}

.card__alignX__left .card__default /* hack for IE */
    {
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
        -webkit-box-align:    start;
        -webkit-align-items:    flex-start;
            -ms-flex-align:    start;
                align-items:    flex-start;
    }

.card__alignX__center
{
    -webkit-box-align:    center;
    -webkit-align-items:    center;
        -ms-flex-align:    center;
            align-items:    center;
    text-align:     center;
}

.card__alignX__center .card__default /* hack for IE */
    {
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
        -webkit-box-align:    center;
        -webkit-align-items:    center;
            -ms-flex-align:    center;
                align-items:    center;
    }

.card__alignX__right
{
    -webkit-box-align:    end;
    -webkit-align-items:    flex-end;
        -ms-flex-align:    end;
            align-items:    flex-end;
    text-align:     right;
}

.card__alignX__right .card__default /* hack for IE */
    {
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
        -webkit-box-align:    end;
        -webkit-align-items:    flex-end;
            -ms-flex-align:    end;
                align-items:    flex-end;
    }

.card__alignY__middle
{
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;
}

.card__alignY__bottom
{
    -webkit-box-pack:    end;
    -webkit-justify-content:    flex-end;
        -ms-flex-pack:    end;
            justify-content:    flex-end;
}

/* padding X */

.card__paddingX__S
{
    padding-left:   8px;
    padding-right:  8px;
}

.card__paddingX__M
{
    padding-left:   16px;
    padding-right:  16px;
}

.card__paddingX__L
{
    padding-left:   24px;
    padding-right:  24px;
}

.card__paddingX__XL
{
    padding-left:   32px;
    padding-right:  32px;
}

.card__paddingX__XXL
{
    padding-left:   40px;
    padding-right:  40px;
}

