    padding-bottom:  16px;
}

.card__paddingY__L
{
    padding-top:     24px;
    padding-bottom:  24px;
}

.card__paddingY__XL
{
    padding-top:     32px;
    padding-bottom:  32px;
}

.card__paddingY__XXL
{
    padding-top:     40px;
    padding-bottom:  40px;
}
