.button__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.button__default:last-child
    {
        margin-bottom:  0;
    }

.button__default
{
    -webkit-appearance:     none !important;
       -moz-appearance:     none !important;
            appearance:     none !important;

    position:       relative;
    display:        table; /* flex doesnt work here */

    width:          auto;
    height:         auto;

    padding:        0;

    border:         none;
    border-radius:  2px;

    background:     transparent;
    cursor:         pointer;
}

.button__default .button__content
    {
        /* Firefox doesnt properly support flex directly
         * on button so we need to set it here
         */
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
        -webkit-box-sizing:         border-box;
                box-sizing:         border-box;
        height:             40px;
        padding:            0 16px;
        text-align:         center;
    }

.button__default .button__label
    {
        font-size:      14px;
        line-height:    16px;
        letter-spacing: 0.5px;

        overflow:       hidden;
        text-overflow:  ellipsis;
        white-space:    nowrap;
    }

.button__default .button__iconContainer
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
    }

.button__default .button__loadingOverlay
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-orient:     horizontal;
        -webkit-box-direction:     normal;
        -webkit-flex-direction:     row;
            -ms-flex-direction:     row;
                flex-direction:     row;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;

        position:           absolute;

        top:                0;
        bottom:             0;

        left:               0;
        right:              0;

        width:              100%;
        height:             100%;
    }

.button__default .button__loadingOverlay .button__spinner
        {
            border-color:   rgba(255, 255, 255, 0.5);
        }

.button__default .button__loadingOverlay .button__spinner::after
            {
                border-top-color:   #ffffff;
            }

.button__iconPosition__right .button__content
    {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
        -webkit-flex-direction: row-reverse;
            -ms-flex-direction: row-reverse;
                flex-direction: row-reverse;
    }

.button__iconPosition__right .button__iconContainer
    {
        margin-left:    4px; /* space between icon and text */
        margin-right:   -8px; /* half of content padding */
    }

.button__iconPosition__left .button__iconContainer
    {
        margin-right:   4px; /* space between icon and text */
        margin-left:    -8px; /* half of content padding */
    }

.button__role__default
{
    background: #19a2e2;
    color:      #ffffff;
}

.button__role__default.button__fakeHovered, .button__role__default:hover
    {
        background: #30abe4;
        color:      #ffffff;
    }

.button__role__default:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.button__role__secondary
{
    background: #eef1f4;
    color:      #8296ae;
}

.button__role__secondary.button__fakeHovered, .button__role__secondary:hover
    {
        background: #cfd7e0;
        color:      #8296ae;
    }

.button__role__secondary:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(89, 116, 148, 0.3);
                box-shadow: 0 0 0 2px rgba(89, 116, 148, 0.3);
    }

.button__role__secondary .button__icon
    {
        fill:   #8296ae;
        color:  #8296ae;
    }

.button__role__secondary .button__loadingOverlay .button__spinner
        {
            border-color:   rgba(130, 150, 174, 0.25);
        }

.button__role__secondary .button__loadingOverlay .button__spinner::after
            {
                border-top-color:   #8296ae;
            }

.button__role__promoted
{
    background: #1bbab0;
    color:      #ffffff;
}

.button__role__promoted.button__fakeHovered, .button__role__promoted:hover
    {
        background: #32c1b8;
        color:      #ffffff;
    }

.button__role__promoted:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(27, 186, 176, 0.3);
                box-shadow: 0 0 0 2px rgba(27, 186, 176, 0.3);
    }

.button__role__critical
{
    background: #ff5460;
    color:      #ffffff;
}

.button__role__critical.button__fakeHovered, .button__role__critical:hover
    {
        background: #ff6d77;
        color:      #ffffff;
    }

.button__role__critical:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.button__role__subtle
{
    height:         auto;
    background:     transparent;
    border-radius:  20px;
}

.button__role__subtle .button__content
    {
        height:         32px;
        border-radius:  20px;
        border:         1px solid #19a2e2;

        -webkit-box-sizing:     border-box;

                box-sizing:     border-box;

        color:          #19a2e2;
    }

.button__role__subtle .button__icon
    {
        fill:   #19a2e2;
        color:  #19a2e2;
    }

.button__role__subtle .button__loadingOverlay .button__spinner
        {
            border-color:   rgba(25, 162, 226, 0.25);
        }

.button__role__subtle .button__loadingOverlay .button__spinner::after
            {
                border-top-color:   #19a2e2;
            }

.button__role__subtle.button__fakeHovered .button__content, .button__role__subtle:hover .button__content
        {
            background-color:   #19a2e2;
            color:              #ffffff;
        }

.button__role__subtle.button__fakeHovered .button__icon, .button__role__subtle:hover .button__icon
        {
            fill:   #ffffff;
        }

.button__role__subtle:focus
    {
        outline:    none;
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.button__role__control
{
    height: auto;
    color:  #4b627d;
}

.button__role__control .button__content
    {
        height:         32px;
        border:         1px solid transparent;
        border-radius:  2px;
        padding: 0 8px;
    }

.button__iconPosition__left.button__role__control .button__iconContainer
        {
            margin-left:    -4px;
        }

.button__iconPosition__right.button__role__control .button__iconContainer
        {
            margin-right:   -4px;
        }

.button__role__control .button__icon
    {
        fill:   #4b627d;
        color:  #4b627d;
    }

.button__role__control.button__fakeHovered, .button__role__control:hover
    {
        color:  #19a2e2;
    }

.button__role__control.button__fakeHovered .button__icon, .button__role__control:hover .button__icon
        {
            fill:   #19a2e2;
            color:  #19a2e2;
        }

.button__role__control .button__loadingOverlay .button__spinner
        {
            border-color:   rgba(75, 98, 125, 0.25);
        }

.button__role__control .button__loadingOverlay .button__spinner::after
            {
                border-top-color:   #4b627d;
            }

.button__role__control:focus
    {
        outline:    none;
    }

.button__role__control:focus .button__content
        {
            border-color:   #19a2e2;
            -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                    box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
        }

.button__loading, .button__disabled
{
    cursor:         default;
    pointer-events: none;
}

.button__loading .button__label, .button__loading .button__iconContainer
    {
        opacity:    0;
    }

.button__disabled.button__role__default
    {
        background: #bae3f6;
        color:      #52b9e9;
    }

.button__disabled.button__role__default .button__icon
        {
            fill:   rgb(83, 185, 233);
            color:  rgb(83, 185, 233);
        }

.button__disabled.button__role__secondary
    {
        color:      #afbccb;
        background: transparent;
    }

.button__disabled.button__role__secondary .button__icon
        {
            fill:   #afbccb;
            color:  #afbccb;
        }

.button__disabled.button__role__promoted
    {
        background: #bbeae7;
        color:      #5fcec7;
    }

.button__disabled.button__role__promoted .button__icon
        {
            fill:   rgb(84, 203, 196);
            color:  rgb(84, 203, 196);
        }

.button__disabled.button__role__critical
    {
        background: #ffcccf;
        color:      #ff8790;
    }

.button__disabled.button__role__critical .button__icon
        {
            fill:   rgb(255, 127, 136);
            color:  rgb(255, 127, 136);
        }

.button__disabled.button__role__subtle .button__content
        {
            color:          #75c7ed;
            background:     #ffffff;
            border-color:   #75c7ed;
        }

.button__disabled.button__role__subtle .button__icon
        {
            fill:   #75c7ed;
            color:  #75c7ed;
        }

.button__disabled.button__role__control .button__content
        {
            color:  #98a8bc;
        }

.button__disabled.button__role__control .button__icon
        {
            fill:   #98a8bc;
            color:  #98a8bc;
        }
