.label__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.label__default:last-child
    {
        margin-bottom:  0;
    }

.label__default
{
    display:        block;
    margin-bottom:  8px;

    width:          auto;
}

.label__default[for]
    {
        cursor: pointer;
    }

legend.label__default /* fix for buggy <legend> element */
    {
        float:  left;
    }

legend.label__default + *
        {
            clear: left;
        }

.label__label
{
    font-size:      12px;
    line-height:    24px;
    letter-spacing: 0.5px;
    color:          #4b627d;
    text-transform: uppercase;
}
