.grid__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.grid__default:last-child
    {
        margin-bottom:  0;
    }

.grid__default
{
    display:        -webkit-box;
    display:        -webkit-flex;
    display:        -ms-flexbox;
    display:        flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align:    stretch;
    -webkit-align-items:    stretch;
        -ms-flex-align:    stretch;
            align-items:    stretch;

    width:          auto;
    max-width:      none;

    margin-bottom:  0;

    /*& > *
    {
        flex:       0 0 100%;
        box-sizing: border-box;
    }*/
}

/*
@media( min-width: 768px )
{ */

.grid__default
    {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-direction: row;
            -ms-flex-direction: row;
                flex-direction: row;

        /*& > *
        {
            flex:   1 0 0%;
        }*/
    }

.grid__alignX__left > *, 
        .grid__alignX__center > *, 
        .grid__alignX__right > * /* Columns */
        {
            -webkit-box-flex:   0;
            -webkit-flex:   0 0 auto;
                -ms-flex:   0 0 auto;
                    flex:   0 0 auto;
        }

.grid__alignX__left
    {
        -webkit-box-pack:    start;
        -webkit-justify-content:    flex-start;
            -ms-flex-pack:    start;
                justify-content:    flex-start;
    }

.grid__alignX__center
    {
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
    }

.grid__alignX__right
    {
        -webkit-box-pack:    end;
        -webkit-justify-content:    flex-end;
            -ms-flex-pack:    end;
                justify-content:    flex-end;
    }

.grid__alignY__top
    {
        -webkit-box-align:    start;
        -webkit-align-items:    flex-start;
            -ms-flex-align:    start;
                align-items:    flex-start;
    }

.grid__alignY__middle
    {
        -webkit-box-align:    center;
        -webkit-align-items:    center;
            -ms-flex-align:    center;
                align-items:    center;
    }

.grid__alignY__bottom
    {
        -webkit-box-align:    end;
        -webkit-align-items:    flex-end;
            -ms-flex-align:    end;
                align-items:    flex-end;
    }

.grid__wrap
    {
        -webkit-flex-wrap:  wrap;
            -ms-flex-wrap:  wrap;
                flex-wrap:  wrap;
    }

/* } */

.grid__gutters__S
{
    margin-left:    -4px;
    margin-right:   -4px;
}

.grid__gutters__S > * /* Columns */
    {
        padding-left:   4px;
        padding-right:  4px;
    }

.grid__gutters__M
{
    margin-left:    -8px;
    margin-right:   -8px;
}

.grid__gutters__M > * /* Columns */
    {
        padding-left:   8px;
        padding-right:  8px;
    }

.grid__gutters__L
{
    margin-left:    -12px;
    margin-right:   -12px;
}

.grid__gutters__L > * /* Columns */
    {
        padding-left:   12px;
        padding-right:  12px;
    }

.grid__spacing__S > * /* Columns */
    {
        /* these paddings will be used by TableCells (yeah, this is ugly) */
        padding-top:    4px;
        padding-bottom: 4px;

        /* override those paddings when *not* a TableCell... */
        margin-top:     -4px;
        margin-bottom:  4px;
    }

.grid__spacing__S:last-child
    {
        margin-bottom:  -8px;
    }

.grid__spacing__M > * /* Columns */
    {
        /* these paddings will be used by TableCells (yeah, this is ugly) */
        padding-top:    8px;
        padding-bottom: 8px;

        /* override those paddings when *not* a TableCell... */
        margin-top:     -8px;
        margin-bottom:  8px;
    }

.grid__spacing__M:last-child
    {
        margin-bottom:  -16px;
    }

.grid__spacing__L > * /* Columns */
    {
        /* these paddings will be used by TableCells (yeah, this is ugly) */
        padding-top:    12px;
        padding-bottom: 12px;

        /* override those paddings when *not* a TableCell... */
        margin-top:     -12px;
        margin-bottom:  12px;
    }

.grid__spacing__L:last-child
    {
        margin-bottom:  -24px;
    }

/* legacy spacing options (deprecated) */

.grid__spacing__default > *, .grid__spacing__h3 > *, .grid__spacing__h4 > *
    {
        padding-top:    8px;
        padding-bottom: 8px;

        margin-top:     -8px;
        margin-bottom:  8px;
    }

.grid__spacing__default:last-child, .grid__spacing__h3:last-child, .grid__spacing__h4:last-child
    {
        margin-bottom:  -16px;
    }

.grid__spacing__label > *
    {
        padding-top:    4px;
        padding-bottom: 4px;

        margin-top:     -4px;
        margin-bottom:  4px;
    }

.grid__spacing__label:last-child
    {
        margin-bottom:  -8px;
    }

.grid__spacing__h1 > *
    {
        padding-top:    28px;
        padding-bottom: 28px;

        margin-top:     -28px;
        margin-bottom:  28px;
    }

.grid__spacing__h1:last-child
    {
        margin-bottom:  -56px;
    }

.grid__spacing__h2 > *
    {
        padding-top:    20px;
        padding-bottom: 20px;

        margin-top:     -20px;
        margin-bottom:  20px;
    }

.grid__spacing__h2:last-child
    {
        margin-bottom:  -40px;
    }

.grid__wrap
{
    -webkit-flex-wrap:  wrap;
        -ms-flex-wrap:  wrap;
            flex-wrap:  wrap;
}

.grid__hasMinHeight
{
    min-height: 87px; /* label + input */
}
