.tableRow__default
{

}

.tableRow__sticky
{
    position:    -webkit-sticky;
    position:    sticky;
    top:         0;
    background:  #ffffff;
    z-index:     2;
}

.tableRow__default.tableRow__active .tableRow__cell
    {
        background: #f6f7f9;
    }

.tableRow__clickable
{
    cursor: pointer;
}

.tableRow__clickable:hover .tableRow__cell, .tableRow__clickable.tableRow__fakeHovered .tableRow__cell
        {
            background: #f6f7f9;
        }
