.text__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.text__default:last-child
    {
        margin-bottom:  0;
    }

.text__default
{
    width:          auto;

    margin-bottom:  8px;

    line-height:    24px;
    text-transform: none;
}

.text__default .text__default
    {
        display:        inline;
        position:       static;
    }

.text__default a:link,.text__default a:visited
    {
        color:              #19a2e2;
        text-decoration:    underline;
    }

.text__default a:-webkit-any-link
    {
        color:              #19a2e2;
        text-decoration:    underline;
    }

.text__default a:-moz-any-link
    {
        color:              #19a2e2;
        text-decoration:    underline;
    }

.text__default a:any-link
    {
        color:              #19a2e2;
        text-decoration:    underline;
    }

.text__default a:link:hover,.text__default a:visited:hover
        {
            text-decoration:    none;
        }

.text__default a:-webkit-any-link:hover
        {
            text-decoration:    none;
        }

.text__default a:-moz-any-link:hover
        {
            text-decoration:    none;
        }

.text__default a:any-link:hover
        {
            text-decoration:    none;
        }

.text__overflowHidden
{
    overflow:       hidden;
    word-wrap:  normal;
    text-overflow:  ellipsis;
}

.text__noWrap
{
    white-space: nowrap;
}

.text__allCaps
{
    text-transform: uppercase;
}

/* ROLES */

.text__role__default
{
    color:  inherit;
}

.text__role__subtle
{
    color:  #546e8c;
}

.text__role__promoted
{
    color:  #1bbab0;
}

.text__role__critical
{
    color:  #ff5460;
}

/* SIZES */

