.tableCell__header /* required until we have a TableHeader text component */
{
    display:    -webkit-box;
    display:    -webkit-flex;
    display:    -ms-flexbox;
    display:    flex;
    font-size:          12px;
    line-height:        24px;
    letter-spacing:     0.5px;
    text-transform:     uppercase;
    text-decoration:    none;
    color:              #42566e;
    cursor:             text;
}

.tableCell__rowHeader
{
    z-index:            1;
}

.tableCell__sticky
{
    position:   -webkit-sticky;
    position:   sticky;
    left:       0;
    background: #ffffff;
    z-index:    1;
}
