.progressBar__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.progressBar__default:last-child
    {
        margin-bottom:  0;
    }

.progressBar__default
{
    height:                     8px;
    border-radius:              20px;
    background-color:           rgba(25, 162, 226, 0.35);
}

.progressBar__fill
{
    display:                    block;
    position:                   absolute;
    left:                       4px;
    height:                     100%;
    max-width:                  calc(100% - 4px);
    background-color:           #19a2e2;
    border-top-right-radius:    20px;
    border-bottom-right-radius: 20px;
}

.progressBar__fill:before
    {
        content:                "";
        display:                block;
        position:               relative;
        left:                   -4px;
        width:                  8px;
        height:                 8px;
        background-color:       #19a2e2;
        border-radius:          50%;
    }
