.scrollBox__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.scrollBox__default:last-child
    {
        margin-bottom:  0;
    }

.scrollBox__default
{
    overflow:   hidden;
}

.scrollBox__default .scrollBox__inner
    {
        /* creates stacking context */
        position:   relative;
        z-index:    0;

        width:      100%;
        height:     100%;
        overflow:   scroll;
    }

/* force webkit scrollbars to be always visible on macOS */

.scrollBox__default .scrollBox__inner::-webkit-scrollbar
        {
            visibility: visible;
        }

.scrollBox__default .scrollBox__scrollBarHorizontal
    {
        position:   absolute;
        bottom:     0;
        left:       0;
    }

.scrollBox__default .scrollBox__scrollBarVertical
    {
        position:   absolute;
        top:        0;
        right:      0;
    }

.scrollBox__scroll__horizontal .scrollBox__inner
    {
        overflow-x:             scroll;
        overflow-y:             hidden;
        overscroll-behavior-x:  contain;
    }

.scrollBox__scroll__vertical .scrollBox__inner
    {
        overflow-x:             hidden;
        overflow-y:             scroll;
        overscroll-behavior-y:  contain;
    }

.scrollBox__scroll__both .scrollBox__scrollBarHorizontal
    {
        width:  calc(100% - 5px);
    }

.scrollBox__scroll__both .scrollBox__scrollBarVertical
    {
        height: calc(100% - 5px);
    }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconDown, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconLeft, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconRight, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconUp
    {
        position:       absolute;
        border-radius:  50%;
        padding:        8px;
        background:     #19a2e2;
        opacity:        0.4;
    }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconDown:hover, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconDown:focus, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconLeft:hover, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconLeft:focus, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconRight:hover, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconRight:focus, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconUp:hover, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconUp:focus
        {
            opacity:    1;
        }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconDown svg, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconLeft svg, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconRight svg, .scrollBox__scrollIndicatorVariant__circle .scrollBox__iconUp svg
        {
            fill:   #ffffff;
        }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconDown
    {
        bottom:     8px;
        left:       50%;
        -webkit-transform:  translateX( -50% );
                transform:  translateX( -50% );
    }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconLeft
    {
        top:        50%;
        left:       8px;
        -webkit-transform:  translateY( -50% );
                transform:  translateY( -50% );
    }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconRight
    {
        top:        50%;
        right:      8px;
        -webkit-transform:  translateY( -50% );
                transform:  translateY( -50% );
    }

.scrollBox__scrollIndicatorVariant__circle .scrollBox__iconUp
    {
        top:        8px;
        left:       50%;
        -webkit-transform:  translateX( -50% );
                transform:  translateX( -50% );
    }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconDown, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconLeft, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconRight, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconUp
    {
        position:         absolute;
        background-color: transparent;
        pointer-events:   none;
        visibility:       hidden;
    }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconDown svg, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconLeft svg, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconRight svg, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconUp svg
        {
            fill:           #546e8c;
            pointer-events: auto;
            visibility:     visible;
            z-index:        1;
        }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconDown::before, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconLeft::before, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconRight::before, .scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconUp::before
        {
            content:    "";
            display:    block;
            position:   absolute;
            visibility: visible;
        }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconDown
    {
        bottom:     0;
        left:       0;
        width:      100%;
    }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconDown::before
        {
            bottom:     0;
            width:      100%;
            height:     72px;

            background: -webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,1)),to(rgba(255,255,255,0)));

            background: linear-gradient(to top, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
        }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconLeft
    {
        top:        0;
        left:       0;
        height:     100%;
    }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconLeft::before
        {
            top:       0;
            left:      0;
            height:    100%;
            width:     72px;

            background: -webkit-gradient(linear, left top, right top, from(rgba(255,255,255,1)),to(rgba(255,255,255,0)));

            background: linear-gradient(to right, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
        }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconRight
    {
        top:        0;
        right:      0;
        height:     100%;
    }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconRight::before
        {
            top:        0;
            right:      0;
            height:     100%;
            width:      72px;

            background: -webkit-gradient(linear, right top, left top, from(rgba(255,255,255,1)),to(rgba(255,255,255,0)));

            background: linear-gradient(to left, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
        }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconUp
    {
        top:        0;
        left:       0;
        width:      100%;
    }

.scrollBox__scrollIndicatorVariant__gradient .scrollBox__iconUp::before
        {
            top:        0;
            width:      100%;
            height:     72px;

            background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,1)),to(rgba(255,255,255,0)));

            background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
        }

