.animate__default
{

}

/*
* FADE
*/

@-webkit-keyframes animate__fadeIn
{
    from
    {
        opacity: 0;
    }

    to
    {
        opacity: 1;
    }
}

@keyframes animate__fadeIn
{
    from
    {
        opacity: 0;
    }

    to
    {
        opacity: 1;
    }
}

.animate__fadeIn
{
    -webkit-animation: animate__fadeIn 1000ms;
            animation: animate__fadeIn 1000ms;
}

@-webkit-keyframes animate__fadeOut
{
    from
    {
        opacity: 1;
    }

    to
    {
        opacity: 0;
    }
}

@keyframes animate__fadeOut
{
    from
    {
        opacity: 1;
    }

    to
    {
        opacity: 0;
    }
}

.animate__fadeOut
{
    -webkit-animation: animate__fadeOut 1000ms;
            animation: animate__fadeOut 1000ms;
}

/*
* SLIDE
*/

@-webkit-keyframes animate__slideInDown
{
    from
    {
        -webkit-transform:  translate3d(0, -100%, 0);
                transform:  translate3d(0, -100%, 0);
        visibility: visible;
    }

    to
    {
        -webkit-transform:  translate3d(0, 0, 0);
                transform:  translate3d(0, 0, 0);
    }
}

@keyframes animate__slideInDown
{
    from
