.inputField__default
{
    font-family: Nessie-Regular, sans-serif;
    font-weight: normal;
    font-style: normal;
}

.inputField__default::-webkit-input-placeholder, .inputField__disabled::-webkit-input-placeholder
{
    font-family: Nessie-RegularIt, sans-serif;
    font-weight: normal;
    font-style: normal;
}

.inputField__default:-ms-input-placeholder, .inputField__disabled:-ms-input-placeholder
{
    font-family: Nessie-RegularIt, sans-serif;
    font-weight: normal;
    font-style: normal;
}

.inputField__default::-ms-input-placeholder, .inputField__disabled::-ms-input-placeholder
{
    font-family: Nessie-RegularIt, sans-serif;
    font-weight: normal;
    font-style: normal;
}

.inputField__default::placeholder, .inputField__disabled::placeholder
{
    font-family: Nessie-RegularIt, sans-serif;
    font-weight: normal;
    font-style: normal;
}

