.inputField__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.inputField__default:last-child
    {
        margin-bottom:  0;
    }

.inputField__default
{
    display:            block;

    -webkit-box-sizing:         border-box;

            box-sizing:         border-box;
    width:              100%;

    border:             1px solid #c7d0db;
    border-radius:      2px;

    padding-top:        11px;
    padding-bottom:     11px;
    padding-left:       16px;
    padding-right:      16px;

    background-color:   #ffffff;

    font-size:          14px;
    color:              #546e8c;
}

/* removes clear and reveal password icons from IE */

.inputField__default::-ms-reveal
    {
        display: none;
    }

.inputField__default::-ms-clear
    {
        display: none;
    }

.inputField__default::-webkit-input-placeholder
    {
        color:         #c7d0db;
    }

.inputField__default:-ms-input-placeholder
    {
        color:         #c7d0db;
    }

.inputField__default::-ms-input-placeholder
    {
        color:         #c7d0db;
    }

.inputField__default::placeholder
    {
        color:         #c7d0db;
    }

.inputField__default:focus, .inputField__default.inputField__fakeHovered
    {
        border-color:   #19a2e2;
        -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);

        outline:        none;
    }

.inputField__default:focus::-webkit-input-placeholder, .inputField__default.inputField__fakeHovered::-webkit-input-placeholder
        {
            color: transparent !important;
        }

.inputField__default:focus:-ms-input-placeholder, .inputField__default.inputField__fakeHovered:-ms-input-placeholder
        {
            color: transparent !important;
        }

.inputField__default:focus::-ms-input-placeholder, .inputField__default.inputField__fakeHovered::-ms-input-placeholder
        {
            color: transparent !important;
        }

.inputField__default:focus::placeholder, .inputField__default.inputField__fakeHovered::placeholder
        {
            color: transparent !important;
        }

textarea.inputField__default
    {
        resize: none;
    }

.inputField__error
{
    border-color:   #ff5460 !important;
    color:          #ff5460 !important;

}

.inputField__error:focus, .inputField__error.inputField__fakeHovered
    {
        -webkit-box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.inputField__disabled
{
    background:     #eef1f4;
    color:          #98a8bc;
    border-color:   #c7d0db;
}

.inputField__disabled:focus
    {
        border-color:   #c7d0db;
    }

.inputField__disabled::-webkit-input-placeholder
    {
        color:         #98a8bc;
    }

.inputField__disabled:-ms-input-placeholder
    {
        color:         #98a8bc;
    }

.inputField__disabled::-ms-input-placeholder
    {
        color:         #98a8bc;
    }

.inputField__disabled::placeholder
    {
        color:         #98a8bc;
    }

.inputField__align__left
{
    text-align: left;
}

.inputField__align__right
{
    text-align: right;
}

textarea.inputField__resizable
    {
        resize: vertical;
    }
