.datePickerItem__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.datePickerItem__default:last-child
    {
        margin-bottom:  0;
    }

.datePickerItem__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;

    -webkit-box-orient:     horizontal;

    -webkit-box-direction:     normal;

    -webkit-flex-direction:     row;

        -ms-flex-direction:     row;

            flex-direction:     row;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;

    padding:            0;
    border:             1px solid transparent;

    background:         #ffffff;

    cursor:             pointer;
}

.datePickerItem__default .datePickerItem__text
    {
        color:                  #4b627d;
        -webkit-font-feature-settings:   lining-nums tabular-nums;
                font-feature-settings:   lining-nums tabular-nums;
        font-variant-numeric:   lining-nums tabular-nums;
        font-size:              14px;
        font-weight:            300;
        line-height:            16px;
    }

.datePickerItem__default.datePickerItem__fakeHovered, .datePickerItem__default:hover
    {
        border-color:   #e8f5fc;
        background:     #e8f5fc;
    }

.datePickerItem__default:focus
    {
        outline:        none;
        border-color:   #19a2e2;
        -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.datePickerItem__type__day
{
    height:             30px;
    width:              32px;
    min-width:          32px; /* IE fix */
    border-radius:      100%;
    margin:             4px;
}

.datePickerItem__type__day:last-child
    {
        margin-bottom:  4px;
    }

.datePickerItem__type__month
{
    height:                 42px;
    width:                  68px;
    border-radius:          3px;
    margin:                 1px;
}

.datePickerItem__type__month:last-child
    {
        margin-bottom:      1px;
    }

.datePickerItem__selected, .datePickerItem__selected.datePickerItem__fakeHovered, .datePickerItem__selected:focus, .datePickerItem__selected:hover
{
    border-color:   #19a2e2;
    background:     #19a2e2;
}

.datePickerItem__selected .datePickerItem__text, .datePickerItem__selected.datePickerItem__fakeHovered .datePickerItem__text, .datePickerItem__selected:focus .datePickerItem__text, .datePickerItem__selected:hover .datePickerItem__text
    {
        color:      #ffffff;
    }

.datePickerItem__disabled, .datePickerItem__disabled.datePickerItem__fakeHovered, .datePickerItem__disabled:focus, .datePickerItem__disabled:hover
{
    border-color:   #eef1f4;
    background:     #eef1f4;
    cursor:         default;
}

.datePickerItem__disabled .datePickerItem__text, .datePickerItem__disabled.datePickerItem__fakeHovered .datePickerItem__text, .datePickerItem__disabled:focus .datePickerItem__text, .datePickerItem__disabled:hover .datePickerItem__text
    {
        color:      #c7d0db;
    }
