.visualTesting .modalContainer
{
    min-width:      1px;
    min-height:     1px;
    top:            0;
    left:           0;
    position:       fixed;
    right:          0;
    bottom:         0;
}

.modalDialog__default
{
    position:           fixed;

    top:                0;
    bottom:             0;
    left:               0;
    right:              0;

    display:            -webkit-box;

    display:            -webkit-flex;

    display:            -ms-flexbox;

    display:            flex;
    -webkit-box-pack:    center;
    -webkit-justify-content:    center;
        -ms-flex-pack:    center;
            justify-content:    center;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;

    overflow-y:         auto;

    z-index:            9999;
    background:         rgba(54, 70, 90, 0.53);
}

.modalDialog__type__carousel
{
    overflow-x:         auto;
}

.modalDialog__type__carousel .modalDialog__header
    {
        border:         1px solid rgba(89, 116, 148, 0.12);
        -webkit-box-shadow:     0 8px 40px rgba(89, 116, 148, 0.12);
                box-shadow:     0 8px 40px rgba(89, 116, 148, 0.12);
    }

.modalDialog__wide .modalDialog__content
{
    width:  900px;
}

.modalDialog__type__carousel .modalDialog__wide .modalDialog__content
    {
        width:  auto;
    }

.modalDialog__showNav.modalDialog__type__carousel .modalDialog__navigation
    {
        display:    block;
    }

.modalDialog__content
{
    width:              500px;
    padding:            24px;
    -webkit-box-sizing:         border-box;
            box-sizing:         border-box;
    border:             1px solid rgba(89, 116, 148, 0.12);
    -webkit-box-shadow:         0 8px 40px rgba(89, 116, 148, 0.12);
            box-shadow:         0 8px 40px rgba(89, 116, 148, 0.12);
    background:         #ffffff;
    text-align:         left;
    z-index:            9999;
    border-radius:      4px;

