.modalDialog__type__carousel .modalDialog__content
    {
        width:              auto;
        min-width:          200px;
        height:             auto;
        max-height:         100%;

        line-height:        0;
    }

/*

only used in type__carousel for now

*/

.modalDialog__header
{
    background-color:   #ffffff;
    padding:            34px 35px 34px 40px;
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-pack:    justify;
    -webkit-justify-content:    space-between;
        -ms-flex-pack:    justify;
            justify-content:    space-between;
    position:           absolute;
    top:                0;
    left:               0;
    width:              100%;
    -webkit-box-sizing:         border-box;
            box-sizing:         border-box;
}

.modalDialog__header button
    {
        -webkit-box-pack: end;
        -webkit-justify-content: flex-end;
            -ms-flex-pack: end;
                justify-content: flex-end;
    }

.modalDialog__title
{
    max-width:          400px;
    position:           relative;
    top:                6px;
    -webkit-flex-basis:         100%;
        -ms-flex-preferred-size:         100%;
            flex-basis:         100%;

    font-size:          18px;
    letter-spacing:     0.2px;
    line-height:        22px;
    color:              #4b627d;
}

.modalDialog__navigation
{
    display:    none;
}

.modalDialog__navigation button
    {
        position:       absolute;
        top:            calc(50% - 20px);

        width:          auto;

        margin:         0;
        border:         none;

        cursor:         pointer;
        z-index:        9999;
    }

.modalDialog__navigation button:first-child
        {
            left:       150px;
            -webkit-transform:  rotate(180deg);
                    transform:  rotate(180deg);
        }

.modalDialog__navigation button:last-child
        {
            right:  150px;
        }
