.valuedTextInput__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.valuedTextInput__default:last-child
    {
        margin-bottom:  0;
    }

.valuedTextInput__default .valuedTextInput__container
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
        -webkit-flex-direction: row;
            -ms-flex-direction: row;
                flex-direction: row;

        margin:         0;

        border-radius:  2px;
    }

.valuedTextInput__default .valuedTextInput__container > *
        {
            margin: 0;
        }

.valuedTextInput__default .valuedTextInput__input
    {
        -webkit-box-flex:           1;
        -webkit-flex:           1 1 auto;
            -ms-flex:           1 1 auto;
                flex:           1 1 auto;
        min-width:      0;

        -webkit-box-shadow:     none;

                box-shadow:     none;
    }

.valuedTextInput__default .valuedTextInput__valueLabel
    {
        display:                    block;
        position:                   relative;

        -webkit-box-flex:                       0;

        -webkit-flex:                       0 0 auto;

            -ms-flex:                       0 0 auto;

                flex:                       0 0 auto;
        width:                      auto;

        border:                     1px solid #c7d0db;
        border-bottom-left-radius:  2px;
        border-top-left-radius:     2px;
        padding:                    0 16px;

        background:                 #ffffff;
        color:                      #546e8c;

        font-size:                  10px;

        line-height:                38px;
        vertical-align:             middle;
        white-space:                nowrap;

        cursor:                     pointer;
    }

.valuedTextInput__default .valuedTextInput__valueLabel::before, .valuedTextInput__default .valuedTextInput__valueLabel::after
        {
            content:        '';

            display:        block;
            position:       absolute;
            top:            11px;

            width:          1px;
            height:         16px;

            background:     #c7d0db;
        }

.valuedTextInput__default .valuedTextInput__valueLabel::before
        {
            left:   0;
        }

.valuedTextInput__default .valuedTextInput__valueLabel::after
        {
            right:  0;
        }

.valuedTextInput__position__left .valuedTextInput__container
    {
        -webkit-box-orient:  horizontal;
        -webkit-box-direction:  reverse;
        -webkit-flex-direction:  row-reverse;
            -ms-flex-direction:  row-reverse;
                flex-direction:  row-reverse;
    }

.valuedTextInput__position__left .valuedTextInput__input
    {
        padding-left:   16px;
    }

.valuedTextInput__position__left .valuedTextInput__valueLabel
    {
        margin-right:   -1px;
        border-right:   none;
    }

.valuedTextInput__position__left .valuedTextInput__valueLabel::before
        {
            display:    none;
        }

.valuedTextInput__position__right .valuedTextInput__input
    {
        padding-right:   16px;
    }

.valuedTextInput__position__right .valuedTextInput__valueLabel
    {
        margin-left:    -1px;
        border-left:    none;
    }

.valuedTextInput__position__right .valuedTextInput__valueLabel::after
        {
            display:    none;
        }

.valuedTextInput__fakeHovered .valuedTextInput__container
    {
        -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.valuedTextInput__fakeHovered .valuedTextInput__valueLabel
    {
        border-color:   #19a2e2;
    }

.valuedTextInput__error .valuedTextInput__valueLabel
    {
        border-color:   #ff5460;
        color:          #ff5460;
    }

.valuedTextInput__error .valuedTextInput__valueLabel::before, .valuedTextInput__error .valuedTextInput__valueLabel::after
        {
            background: #ff5460;
        }

.valuedTextInput__error .valuedTextInput__input
    {
        -webkit-box-shadow:     none !important;
                box-shadow:     none !important;
    }

.valuedTextInput__error.valuedTextInput__fakeHovered .valuedTextInput__container
    {
        -webkit-box-shadow:     0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow:     0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.valuedTextInput__disabled .valuedTextInput__valueLabel, .valuedTextInput__disabled .valuedTextInput__input, .valuedTextInput__disabled:hover .valuedTextInput__valueLabel, .valuedTextInput__disabled:hover .valuedTextInput__input
    {
        background:     #eef1f4;
        border-color:   #c7d0db;
        pointer-events: none;
    }
