.navItem__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.navItem__default:last-child
    {
        margin-bottom:  0;
    }

.navItem__default
{
    display:        block;
    list-style:     none;
    width:          auto;
    margin-bottom:  0;
    background:     #ffffff;
}

.navItem__default:hover, .navItem__default.navItem__current
    {
        background-color:  #e8f5fc;
    }

.navItem__disabled
{
    pointer-events: none;

}

.navItem__disabled > .navItem__link
    {
        color:  #c7d0db !important;
        cursor: default !important;
    }

.navItem__disabled > .navItem__link > .navItem__icon
        {
            opacity:    0.5;
        }

.navItem__role__default > .navItem__link, .navItem__role__primary > .navItem__link
    {
        padding:    7px;
    }

.navItem__role__default > .navItem__link[href]:hover, .navItem__role__default > .navItem__link[href]:focus, .navItem__role__default > .navItem__link[href]:active, .navItem__role__default.navItem__fakeHovered > .navItem__link, .navItem__role__default.navItem__open > .navItem__link, .navItem__role__default.navItem__current > .navItem__link, .navItem__role__primary > .navItem__link[href]:hover, .navItem__role__primary > .navItem__link[href]:focus, .navItem__role__primary > .navItem__link[href]:active, .navItem__role__primary.navItem__fakeHovered > .navItem__link, .navItem__role__primary.navItem__open > .navItem__link, .navItem__role__primary.navItem__current > .navItem__link
    {
        color:  #19a2e2;
    }

.navItem__role__default.navItem__fakeHovered, .navItem__role__default.navItem__open, .navItem__role__primary.navItem__fakeHovered, .navItem__role__primary.navItem__open
    {
        background-color:  #e8f5fc;
    }

.navItem__role__default > .navItem__link, .navItem__role__primary > .navItem__link
    {
        border:             1px solid transparent;
        border-radius:      2px;
    }

.navItem__role__default > .navItem__link:focus, .navItem__role__primary > .navItem__link:focus
        {
            outline:        none;
            border-color:   #19a2e2;
            -webkit-box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
                    box-shadow:     0 0 0 2px rgba(25, 162, 226, 0.3);
        }

.navItem__role__sub
{
    background:            #ffffff;
    border-radius:         6px;
}

.navItem__role__sub > .navItem__link
    {
        padding-top:       0;
        padding-right:     15px;
        padding-bottom:    0;
        padding-left:      15px;

        border-radius:     2px;

        line-height:       47px;
        color:             #4b627d;
    }

.navItem__role__sub > .navItem__link:focus
        {
            outline: none;
        }

.navItem__role__sub > .navItem__link:visited
        {
            color:         #4b627d;
        }

.navItem__role__sub > .navItem__link[href]:hover, .navItem__role__sub > .navItem__link[href]:focus, .navItem__role__sub > .navItem__link[href]:active, .navItem__role__sub.navItem__fakeHovered > .navItem__link, .navItem__role__sub.navItem__open > .navItem__link, .navItem__role__sub.navItem__current > .navItem__link
    {
        background-color:   #19a2e2;
        color:              #ffffff;
    }

.navItem__open:not(.navItem__role__sub):after
    {
        content:            '';
        position:           absolute;
        display:            inline-block;

        height:             8px;
        width:              100%;

        background-color:   transparent;
    }

.navItem__open .navItem__dropdown
    {
        left:   auto;
    }

.navItem__open.navItem__dropdownAlign__right .navItem__dropdown
    {
        right:  0;
    }

.navItem__link
{
    display:            block;
    white-space:        nowrap;
    text-decoration:    none !important;
    cursor:             text;

    font-size:      14px;
    line-height:    16px;
    color:          #8296ae;
}

.navItem__link[href]
    {
        cursor: pointer;
    }

.navItem__link:visited
    {
        color:  #8296ae;
    }

.navItem__dropdown
{
    position:   absolute;
    left:       -99999px;
    margin-top: 8px;
}

.navItem__hasIcon > .navItem__link
    {
        padding-top:    12px;
        padding-right:  23px;
        padding-bottom: 14px;
        padding-left:   23px;
    }

.navItem__hasIcon > .navItem__link > .navItem__icon
    {
        margin:        auto;
        margin-bottom: 4px;
    }

.navItem__hasIcon > .navItem__link[href]:hover > .navItem__icon, .navItem__hasIcon > .navItem__link[href]:active > .navItem__icon, .navItem__hasIcon > .navItem__link[href]:focus > .navItem__icon, .navItem__hasIcon.navItem__open > .navItem__link > .navItem__icon, .navItem__hasIcon.navItem__fakeHovered > .navItem__link > .navItem__icon, .navItem__hasIcon.navItem__current > .navItem__link > .navItem__icon
    {
        fill: #19a2e2;
    }

.navItem__hasIcon > .navItem__link > .navItem__icon, .navItem__hasIcon.navItem__disabled > .navItem__link > .navItem__icon
    {
        fill: #c7d0db;
    }
