.inputContainer__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.inputContainer__default:last-child
    {
        margin-bottom:  0;
    }

.inputContainer__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    font:               inherit;
    line-height:        inherit;
}

.inputContainer__default .inputContainer__container
    {
        -webkit-box-flex:           1;
        -webkit-flex:           1 1 auto;
            -ms-flex:           1 1 auto;
                flex:           1 1 auto;
        min-width:      0;
    }

.inputContainer__default .inputContainer__label
    {
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
    }

.inputContainer__labelPosition__top
{
    -webkit-box-orient:     vertical;
    -webkit-box-direction:     normal;
    -webkit-flex-direction:     column;
        -ms-flex-direction:     column;
            flex-direction:     column;
}

.inputContainer__labelPosition__top .inputContainer__label
    {
        -webkit-align-self:     flex-start;
            -ms-flex-item-align:     start;
                align-self:     flex-start;
    }

.inputContainer__labelPosition__left, .inputContainer__labelPosition__right
{
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;
}

.inputContainer__labelPosition__left .inputContainer__label, .inputContainer__labelPosition__right .inputContainer__label
    {
        max-width:      50%;
        margin-bottom:  0;
    }

.inputContainer__labelPosition__left > .inputContainer__container
    {
        margin-left:    10px;
    }

.inputContainer__labelPosition__right
{
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -webkit-flex-direction: row-reverse;
        -ms-flex-direction: row-reverse;
            flex-direction: row-reverse;
}

.inputContainer__labelPosition__right > .inputContainer__container
    {
        margin-right:   10px;
    }
