.iconWithTooltip__default
{
    display:        -webkit-box;
    display:        -webkit-flex;
    display:        -ms-flexbox;
    display:        flex;
    -webkit-box-align:    center;
    -webkit-align-items:    center;
        -ms-flex-align:    center;
            align-items:    center;

    position:       relative;

    max-width:      100%;

    font:           inherit;
    line-height:    inherit;

    margin-bottom:   16px;
}

.iconWithTooltip__default:last-child
    {
        margin-bottom:  0;
    }

.iconWithTooltip__position__topRight > .iconWithTooltip__content, .iconWithTooltip__position__topLeft > .iconWithTooltip__content
    {
        -webkit-box-flex:   1;
        -webkit-flex:   1 0 0%;
            -ms-flex:   1 0 0%;
                flex:   1 0 0%;
    }

.iconWithTooltip__iconVisible > .iconWithTooltip__iconWithTooltip
    {
        display:    block;
    }

.iconWithTooltip__iconVisible.iconWithTooltip__position__right, .iconWithTooltip__iconVisible.iconWithTooltip__position__left
    {
        -webkit-box-pack:    start;
        -webkit-justify-content:    flex-start;
            -ms-flex-pack:    start;
                justify-content:    flex-start;
    }

.iconWithTooltip__iconVisible.iconWithTooltip__position__right > .iconWithTooltip__content, .iconWithTooltip__iconVisible.iconWithTooltip__position__left > .iconWithTooltip__content
        {
            -webkit-box-flex:           0;
            -webkit-flex:           0 1 auto;
                -ms-flex:           0 1 auto;
                    flex:           0 1 auto;
            max-width:      100%;
        }

.iconWithTooltip__iconVisible.iconWithTooltip__position__right > .iconWithTooltip__content + .iconWithTooltip__iconWithTooltip, .iconWithTooltip__iconVisible.iconWithTooltip__position__left > .iconWithTooltip__content + .iconWithTooltip__iconWithTooltip
        {
            position:       relative;
            margin-top:     -50%;
            margin-bottom:  -50%;
        }

.iconWithTooltip__iconVisible.iconWithTooltip__position__left
    {
        -webkit-box-orient:     horizontal;
        -webkit-box-direction:     reverse;
        -webkit-flex-direction:     row-reverse;
            -ms-flex-direction:     row-reverse;
                flex-direction:     row-reverse;
        -webkit-box-pack:    end;
        -webkit-justify-content:    flex-end;
            -ms-flex-pack:    end;
                justify-content:    flex-end;
    }

.iconWithTooltip__iconVisible.iconWithTooltip__position__left > .iconWithTooltip__content
        {
            margin-left:    8px;
        }

.iconWithTooltip__iconVisible.iconWithTooltip__position__right > .iconWithTooltip__content
        {
            margin-right:   8px;
        }

.iconWithTooltip__iconVisible.iconWithTooltip__position__topRight > .iconWithTooltip__iconWithTooltip, .iconWithTooltip__iconVisible.iconWithTooltip__position__topLeft > .iconWithTooltip__iconWithTooltip
        {
            position:   absolute;
            top:        -16px;
        }

.iconWithTooltip__iconVisible.iconWithTooltip__position__topRight > .iconWithTooltip__iconWithTooltip
        {
            right:  -16px;
        }

.iconWithTooltip__iconVisible.iconWithTooltip__position__topLeft > .iconWithTooltip__iconWithTooltip
        {
            left:   -16px;
        }

.iconWithTooltip__content
{
    -webkit-box-flex:                1;
    -webkit-flex:                1 1 auto;
        -ms-flex:                1 1 auto;
            flex:                1 1 auto;
    max-width:           100%;
    min-width:           0;
}

.iconWithTooltip__iconWithTooltip
{
    display:            none;
    -webkit-box-flex:               0;
    -webkit-flex:               0 0 auto;
        -ms-flex:               0 0 auto;
            flex:               0 0 auto;
    z-index:            1;
}
