.radio__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.radio__default:last-child
    {
        margin-bottom:  0;
    }

.radio__default
{
    padding-top:    8px;
    padding-bottom: 8px;

    width:          auto;
}

.radio__default .radio__input /* the (hidden) native input */
    {
        position:   absolute;
        left:       -9999px;
    }

.radio__default .radio__label /* label is the styled checkbox/radio */
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-align:    center;
        -webkit-align-items:    center;
            -ms-flex-align:    center;
                align-items:    center;

        position:       relative;

        height:         24px;

        cursor:         pointer;
    }

.radio__default .radio__label::before
        {
            content:        '';

            display:        block;
            position:       relative;

            -webkit-box-sizing:     border-box;

                    box-sizing:     border-box;
            width:          24px;
            height:         24px;

            border:         1px solid #97d5f1;
            border-radius:  2px;

            background:     #ffffff;
        }

.radio__default .radio__label::after
        {
            content:    '';

            display:    none;
            position:   absolute;

            top:        0;
            left:       0;

            width:      24px;
            height:     24px;
        }

.radio__default .radio__labelContent
    {
        display:        block;
        margin-left:    8px;
    }

.radio__default .radio__labelText
    {
        color:  #4b627d;
    }

.radio__default .radio__input:checked + .radio__label::before, .radio__default .radio__input:focus + .radio__label::before, .radio__default.radio__fakeHovered .radio__label::before
    {
        border-color:   #19a2e2;
    }

.radio__default .radio__input:focus + .radio__label::before, .radio__default.radio__fakeHovered .radio__label::before
    {
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.radio__error .radio__label::before
    {
        border: 1px solid #ff5460 !important;
    }

.radio__error .radio__input:focus + .radio__label::before, .radio__error.radio__fakeHovered .radio__label::before
    {
        -webkit-box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.radio__disabled .radio__label
    {
        cursor: default;
    }

.radio__disabled .radio__label::before
        {
            border-color:   #c7d0db !important;
        }

.radio__disabled .radio__labelText
    {
        color:  #c7d0db;
    }

.radio__default .radio__label::before, .radio__default .radio__label::after
    {
        border-radius:  50%;
    }

.radio__default .radio__input:checked + .radio__label::after
    {
        display:    block;

        top:        6px;
        left:       6px;

        width:      12px;
        height:     12px;

        background: #19a2e2;
    }

.radio__error .radio__input:checked + .radio__label::after
    {
        background: #ff5460;
    }

.radio__disabled .radio__input:checked + .radio__label::after
    {
        background: #c7d0db;
    }
