.tag__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.tag__default:last-child
    {
        margin-bottom:  0;
    }

.tag__default
{
    display:        -webkit-box;
    display:        -webkit-flex;
    display:        -ms-flexbox;
    display:        flex;
    -webkit-box-align:    center;
    -webkit-align-items:    center;
        -ms-flex-align:    center;
            align-items:    center;

    width:          auto;
    height:         32px;

    padding-left:   16px;

    background:     #e8f5fc;

    border-radius:  2px;
}

.tag__default .tag__label
    {

        letter-spacing: 0.4px;
        white-space:    nowrap;
        color:          #19a2e2;
    }

.tag__default .tag__delete
    {
        margin-left:    8px;
        margin-right:   8px;
    }

.tag__default .tag__delete svg
        {
            fill:       #19a2e2;
        }

.tag__default > *
    {
        margin-bottom:  0;
    }

.tag__disabled
{
    background:         #eef1f4;
}

.tag__disabled .tag__label
    {
        color:          #98a8bc;
    }
