.table__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.table__default:last-child
    {
        margin-bottom:  0;
    }

.table__default
{
    -webkit-font-feature-settings:	lining-nums tabular-nums;
            font-feature-settings:	lining-nums tabular-nums;
    font-variant-numeric:	lining-nums tabular-nums;
    word-wrap:          break-word;
}

.table__default .table__row
    {
        margin-bottom: 0;
    }

.table__borders__rows, .table__borders__cells, .table__borders__rowDivider, .table__borders__columnDivider, .table__zebra
{
    margin-bottom:  16px;
}

.table__borders__rows .table__row, .table__borders__rows .table__headerRow, .table__borders__cells .table__row, .table__borders__cells .table__headerRow, .table__borders__rowDivider .table__row, .table__borders__rowDivider .table__headerRow, .table__borders__columnDivider .table__row, .table__borders__columnDivider .table__headerRow, .table__zebra .table__row, .table__zebra .table__headerRow
    {
        margin: 0;
    }

.table__borders__rows .table__cell, .table__borders__cells .table__cell, .table__borders__rowDivider .table__cell, .table__borders__columnDivider .table__cell, .table__zebra .table__cell
    {
        margin:     0;
        background: #ffffff;
    }

.table__borders__none .table__row, .table__borders__none .table__cell
    {
        border: none;
    }

.table__borders__cells .table__row
    {
        border: 1px solid rgba(89, 116, 148, 0.12);
    }

.table__borders__cells .table__row + .table__row
        {
            border-top: none;
        }

.table__borders__cells .table__row .table__cell:not(:last-child)
            {
                border-right: 1px solid rgba(89, 116, 148, 0.12);
            }

.table__borders__cells .table__row .table__cell + .table__cell
            {
                border-left: none;
            }

.table__borders__columnDivider .table__cell, .table__borders__columnDivider .table__row .table__cell
    {
        border-right: 1px solid rgba(89, 116, 148, 0.12);
    }

.table__borders__columnDivider .table__cell + .table__cell, .table__borders__columnDivider .table__row .table__cell + .table__cell
        {
            border-left: none;
        }

.table__borders__columnDivider .table__cell:last-child, .table__borders__columnDivider .table__row .table__cell:last-child
        {
            border-right: none;
        }

.table__borders__rows .table__row
    {
        border: 1px solid rgba(89, 116, 148, 0.12);
    }

.table__borders__rows .table__row + .table__row
        {
            border-top: none;
        }

.table__borders__rowDivider .table__row:not(:last-child), .table__borders__rowDivider .table__headerRow
    {
        border-bottom: 1px solid rgba(89, 116, 148, 0.12);
    }

.table__borders__rowDivider .table__row:not(:last-child) + .table__row, .table__borders__rowDivider .table__headerRow + .table__row
        {
            border-top: none;
        }

.table__zebra .table__row:nth-child( odd ) .table__cell
    {
        background: #f6f7f9;
    }
