.slider__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.slider__default:last-child
    {
        margin-bottom:  0;
    }

.slider__default .slider__inputContainer
    {
        position:   absolute;
        left:       -99999px;
    }

.slider__default .slider__labelContainer
    {
        margin-bottom:  8px;
    }

.slider__default .slider__handle, .slider__default .slider__trackFill
    {
        position:   absolute;
    }

.slider__default .slider__trackContainer
    {
        display:    -webkit-box;
        display:    -webkit-flex;
        display:    -ms-flexbox;
        display:    flex;
    }

.slider__default .slider__track
    {
        position:       relative;

        width:          2px;
        border-radius:  1px;

        background:     #eef1f4;

        -webkit-user-select:    none;

           -moz-user-select:    none;

            -ms-user-select:    none;

                user-select:    none;
        cursor:         pointer;

        -webkit-tap-highlight-color:    transparent;
    }

.slider__default .slider__trackFill
    {
        border-radius:  1px;
        background:     #19a2e2;
    }

.slider__default .slider__handle
    {
        width:          16px;
        height:         16px;

        border-radius:  50%;
        background:     #19a2e2;

        cursor:         -webkit-grab;

        cursor:         grab;

        z-index:        1;
    }

.slider__default .slider__handle::after
        {
            content:        '';
            display:        block;

            position:       absolute;
            top:            50%;
            left:           50%;
            -webkit-transform:      translateX( -50%) translateY( -50% );
                    transform:      translateX( -50%) translateY( -50% );

            width:          24px;
            height:         24px;

            background:     #1589c0;

            border-radius:  50%;

            opacity:        0;
            -webkit-transition:     opacity 450ms;
            transition:     opacity 450ms;
        }

.slider__default .slider__handle:active
        {
            cursor: -webkit-grabbing;
            cursor: grabbing;
        }

.slider__default .slider__handleFocus::after
        {
            opacity:    0.2;
        }

.slider__default .slider__handleLabel
    {
        display:    none;
    }

/* Ticks */

.slider__default .slider__ticksContainer
    {
        position:   relative;
        width:      100%;
        height:     100%;
    }

.slider__default .slider__tick
    {
        position:   absolute;
        background: #ffffff;
        font-size:  0;
    }

/* Step labels */

.slider__default .slider__stepLabelsContainer
    {
        position:   relative;
    }

.slider__default .slider__stepLabel
    {
        position:       absolute;

        max-width:      100%;

        white-space:    nowrap;
        overflow:       hidden;
        text-overflow:  ellipsis;
        font-size:      14px;
        line-height:    16px;
        color:          #98a8bc;
    }

.slider__orientation__horizontal .slider__trackContainer
    {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
            -ms-flex-direction: column;
                flex-direction: column;
    }

.slider__orientation__horizontal .slider__track, .slider__orientation__horizontal .slider__stepLabelsContainer
    {
        width:          calc(100% - 16px);

        margin-left:    8px;
        margin-right:   8px;
    }

.slider__orientation__horizontal .slider__track
    {
        height:         2px;

        margin-top:     19px;
        margin-bottom:  19px;
    }

.slider__orientation__horizontal .slider__handle
    {
        top:        50%;
        -webkit-transform:  translateX( -50% ) translateY( -50% );
                transform:  translateX( -50% ) translateY( -50% );
    }

.slider__orientation__horizontal .slider__trackFill
    {
        height: 2px;
    }

.slider__orientation__horizontal .slider__stepLabelsContainer
    {
        height:         16px;

        margin-left:    8px;
        margin-right:   8px;
    }

.slider__orientation__horizontal .slider__stepLabelsContainer
    {
        margin-bottom:  8px;
    }

.slider__orientation__horizontal .slider__track + .slider__stepLabelsContainer
    {
        margin-top:     8px;
        margin-bottom:  0;
    }

.slider__orientation__horizontal .slider__stepLabel
    {
        -webkit-transform:  translateX( -50% );
                transform:  translateX( -50% );
    }

.slider__orientation__horizontal .slider__tick
    {
        width:  8px;
        height: 2px;

        top:        50%;
        -webkit-transform:  translateX( -50% ) translateY( -50% );
                transform:  translateX( -50% ) translateY( -50% );
    }

.slider__orientation__vertical
{
    width:  auto;
    height: 300px;
}

.slider__orientation__vertical .slider__trackContainer
    {
        height: 100%;
    }

.slider__orientation__vertical .slider__track
    {
        width:          2px;
        height:         calc(100% - 16px);

        margin-top:     8px;
        margin-bottom:  8px;
        margin-left:    19px;
        margin-right:   19px;
    }

.slider__orientation__vertical .slider__trackFill
    {
        width:  2px;
    }

.slider__orientation__vertical .slider__handle
    {
        left:       50%;
        -webkit-transform:  translateX( -50% ) translateY( 50% );
                transform:  translateX( -50% ) translateY( 50% );
    }

.slider__orientation__vertical .slider__stepLabelsContainer
    {
        display:    none;
    }

.slider__orientation__vertical .slider__tick
    {
        width:  2px;
        height: 8px;

        left:       50%;
        -webkit-transform:  translateX( -50% ) translateY( 50% );
                transform:  translateX( -50% ) translateY( 50% );
    }

.slider__hasHandleLabels .slider__handleLabel
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        position:           relative;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;

        color:              #19a2e2;
        line-height:        16px;

        cursor:             default;
        -webkit-user-select:        none;
           -moz-user-select:        none;
            -ms-user-select:        none;
                user-select:        none;
    }

.slider__handleLabelPosition__top .slider__handleLabel
    {
        top:        -4px;
        -webkit-transform:  translateY( -100% );
                transform:  translateY( -100% );
    }

.slider__handleLabelPosition__bottom .slider__handleLabel
    {
        bottom:     -4px;
        -webkit-transform:  translateY( 100% );
                transform:  translateY( 100% );
    }

.slider__handleLabelPosition__right .slider__handleLabel
    {
        right:      -4px;
        -webkit-transform:  translateX( 100% );
                transform:  translateX( 100% );
    }

.slider__handleLabelPosition__left .slider__handleLabel
    {
        left:       -4px;
        -webkit-transform:  translateX( -100% );
                transform:  translateX( -100% );

    }

.slider__error .slider__trackFill
    {
        background: rgba(255, 84, 96, 0.35);
    }

.slider__error .slider__handle
    {
        background: #ff5460 !important;
    }

.slider__error .slider__handle::after
        {
            background: #d94751;
        }

.slider__error .slider__handleLabel
    {
        color:  #ff5460;
    }

.slider__grabbing .slider__handle, .slider__grabbing .slider__trackFill
    {
        -webkit-transition: none;
        transition: none;
    }

.slider__disabled .slider__track, .slider__disabled .slider__handle
    {
        cursor: default !important;
    }

.slider__disabled .slider__handle
    {
        background: #bae3f6;
    }

.slider__disabled .slider__handle::after
        {
            display:    none;
        }

.slider__disabled .slider__trackFill
    {
        background: #bae3f6;
    }

.slider__disabled .slider__stepLabel
    {
        color:  #c7d0db;
    }
