.checkableGroup__default
{
    /*  pladeholder */
}

.checkableGroup__list
{
    display:			-webkit-box;
    display:			-webkit-flex;
    display:			-ms-flexbox;
    display:			flex;
    -webkit-box-orient:		horizontal;
    -webkit-box-direction:		normal;
    -webkit-flex-direction:		row;
        -ms-flex-direction:		row;
            flex-direction:		row;
    -webkit-flex-wrap:			wrap;
        -ms-flex-wrap:			wrap;
            flex-wrap:			wrap;
    -webkit-box-pack:	start;
    -webkit-justify-content:	flex-start;
        -ms-flex-pack:	start;
            justify-content:	flex-start;
    -webkit-box-align:		start;
    -webkit-align-items:		flex-start;
        -ms-flex-align:		start;
            align-items:		flex-start;

    margin:             0;
    padding:            0;

    list-style-type:	none;
}

.checkableGroup__listItem
{
    display:		block;

    margin:         0;
    margin-right:	30px;
}

.checkableGroup__listItem:last-child
    {
        margin:	0;
    }

.checkableGroup__layout__vertical .checkableGroup__list
	{
        -webkit-box-orient:	vertical;
        -webkit-box-direction:	normal;
        -webkit-flex-direction:	column;
            -ms-flex-direction:	column;
                flex-direction:	column;
    }

.checkableGroup__layout__vertical .checkableGroup__listItem
    {
        margin: 0;
    }
