.scrollBar__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.scrollBar__default:last-child
    {
        margin-bottom:  0;
    }

.scrollBar__default
{
    width:          100%;
    height:         100%;
    margin-bottom:  0;
    background:     #eef1f4;
}

.scrollBar__default .scrollBar__thumb
    {
        position:   absolute;
        background: #8296ae;
    }

.scrollBar__orientation__horizontal
{
    height: 5px;
}

.scrollBar__orientation__horizontal .scrollBar__thumb
    {
        top:        0;
        bottom:     0;
        min-width:  20px;
    }

.scrollBar__orientation__vertical
{
    width:  5px;
}

.scrollBar__orientation__vertical .scrollBar__thumb
    {
        left:       0;
        right:      0;
        min-height: 20px;
    }
