.navDropdown__default /* extends NavList css */
{
    width:                  auto;
    max-width:              none;
    min-width:              150px;
    max-height:             50vh;

    border:                 1px solid rgba(89, 116, 148, 0.12);
    background:             #ffffff;
    -webkit-box-shadow:             0 8px 40px rgba(89, 116, 148, 0.12);
            box-shadow:             0 8px 40px rgba(89, 116, 148, 0.12);

    padding:                16px;

    overflow-y:             auto;
    overscroll-behavior-y:  contain;

    z-index:                999;
}
