.textInputWithIcon__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.textInputWithIcon__default:last-child
    {
        margin-bottom:  0;
    }

.textInputWithIcon__default .textInputWithIcon__icon
    {
        position:   absolute;
        top:        50%;
        margin-top: -12px;
    }

.textInputWithIcon__container
{
    display:        -webkit-box;
    display:        -webkit-flex;
    display:        -ms-flexbox;
    display:        flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-align:    center;
    -webkit-align-items:    center;
        -ms-flex-align:    center;
            align-items:    center;
}

.textInputWithIcon__container .textInputWithIcon__input
    {
        -webkit-box-flex:           1;
        -webkit-flex:           1 1 auto;
            -ms-flex:           1 1 auto;
                flex:           1 1 auto;
        min-width:      0;
        margin-bottom:  0;
    }

.textInputWithIcon__position__left .textInputWithIcon__input
    {
        padding-left:   41px;
    }

.textInputWithIcon__position__left .textInputWithIcon__icon
    {
        left:   9px;
    }

.textInputWithIcon__position__right .textInputWithIcon__input
    {
        padding-right:   41px;
    }

.textInputWithIcon__position__right .textInputWithIcon__icon
    {
        right:  9px;
    }
