.listBoxOption__default
{
    display:            -webkit-box;
    display:            -webkit-flex;
    display:            -ms-flexbox;
    display:            flex;
    -webkit-box-align:        center;
    -webkit-align-items:        center;
        -ms-flex-align:        center;
            align-items:        center;

    margin:             0;
    padding:            8px 16px;

    cursor:             pointer;
}

.listBoxOption__default .listBoxOption__icon
    {
        -webkit-box-flex:           0;
        -webkit-flex:           0 0 auto;
            -ms-flex:           0 0 auto;
                flex:           0 0 auto;
        margin-right:   16px;
    }

.listBoxOption__default .listBoxOption__textContainer
    {
        -webkit-box-flex:           1;
        -webkit-flex:           1 0 0%;
            -ms-flex:           1 0 0%;
                flex:           1 0 0%;
        min-width:      0;
    }

.listBoxOption__default .listBoxOption__optionText
    {
        margin:         0;
        color:          #597494;
    }

.listBoxOption__default:hover, .listBoxOption__default.listBoxOption__active
    {
        background:     #19a2e2;
    }

.listBoxOption__default:hover .listBoxOption__optionText, .listBoxOption__default:hover .listBoxOption__description, .listBoxOption__default.listBoxOption__active .listBoxOption__optionText, .listBoxOption__default.listBoxOption__active .listBoxOption__description
        {
            color:      #ffffff;
        }

.listBoxOption__withDescription .listBoxOption__optionText
    {
        font-size:      18px;
        line-height:    24px;
        color:          #19a2e2;
    }

.listBoxOption__selected
{
    background:         rgba(25, 162, 226, 0.1);
}

.listBoxOption__disabled
{
    pointer-events:     none;
    cursor:             auto;
}

.listBoxOption__disabled .listBoxOption__optionText
    {
        color:          #c7d0db;
    }

.listBoxOption__disabled.listBoxOption__withDescription .listBoxOption__optionText, .listBoxOption__disabled .listBoxOption__description
    {
        color:          #98a8bc;
    }
