.tabs__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.tabs__default:last-child
    {
        margin-bottom:  0;
    }

.tabs__default .tabs__header
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-align:    end;
        -webkit-align-items:    flex-end;
            -ms-flex-align:    end;
                align-items:    flex-end;
        max-width:      100%;
        border-bottom:  solid 1px #c7d0db;
        margin-bottom:  16px;
    }

.tabs__default .tabs__header > *
        {
            margin-bottom:  0;
        }

.tabs__default .tabs__tabsContainer
    {
        -webkit-box-flex:           1;
        -webkit-flex:           auto;
            -ms-flex:           auto;
                flex:           auto;
        margin-bottom:  -1px;
    }

.tabs__default .tabs__tabs
    {
        display:    -webkit-box;
        display:    -webkit-flex;
        display:    -ms-flexbox;
        display:    flex;
    }

.tabs__default .tabs__tabs > *
        {
            -webkit-box-flex:           0;
            -webkit-flex:           none;
                -ms-flex:           none;
                    flex:           none;
            margin-bottom:  0;
        }

.tabs__default .tabs__secondaryControls
    {
        -webkit-box-flex:           0;
        -webkit-flex:           none;
            -ms-flex:           none;
                flex:           none;
        margin-left:    16px;
        margin-bottom:  16px;
    }

.tabs__default .tabs__content
    {
        /* nothing to do here */
    }
