.toggleButton__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.toggleButton__default:last-child
    {
        margin-bottom:  0;
    }

.toggleButton__default
{
    -webkit-appearance:     none !important;
       -moz-appearance:     none !important;
            appearance:     none !important;
    display:        table; /* flex is not well supported on <button>... */
    width:          auto;
    border:         none;
    padding:        0;
    border-radius:  2px;
    background:     transparent;
    cursor:         pointer;
}

.toggleButton__default .toggleButton__flexContainer /* ...so we set it here instead */
    {
        display:            -webkit-box;
        display:            -webkit-flex;
        display:            -ms-flexbox;
        display:            flex;
        -webkit-box-pack:    center;
        -webkit-justify-content:    center;
            -ms-flex-pack:    center;
                justify-content:    center;
        -webkit-box-align:        center;
        -webkit-align-items:        center;
            -ms-flex-align:        center;
                align-items:        center;
        -webkit-box-sizing:         border-box;
                box-sizing:         border-box;
        height:             40px;
        text-align:         center;
    }

.toggleButton__default .toggleButton__labelContainer
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
            -ms-flex-direction: column;
                flex-direction: column;
    }

.toggleButton__default .toggleButton__title, .toggleButton__default .toggleButton__subLabel, .toggleButton__default
    {
        color:  #4b627d;
    }

.toggleButton__default .toggleButton__icon
    {
        fill:   #4b627d;
    }

.toggleButton__default:focus
    {
        outline:    none;

        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);

                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.toggleButton__default:focus .toggleButton__flexContainer
        {
            border-color: #19a2e2;
        }

.toggleButton__role__primary .toggleButton__flexContainer
    {
        border:         1px solid #c7d0db;
        border-radius:  2px;
        padding:        0 16px;
        background:     #ffffff;
    }

.toggleButton__role__primary .toggleButton__title
    {
        font-size:      14px;
        line-height:    16px;
    }

.toggleButton__role__primary .toggleButton__subLabel
    {
        color:         #98a8bc;
        font-size:     11px;
        line-height:   14px;
    }

.toggleButton__role__primary .toggleButton__title + .toggleButton__subLabel
    {
        margin-top: 2px;
    }

.toggleButton__role__primary:not(.toggleButton__disabled):hover .toggleButton__flexContainer
        {
            background: #eef1f4;
        }

.toggleButton__role__primary.toggleButton__pressed:not(:hover) .toggleButton__flexContainer
        {
            border-color:   #19a2e2;
            background:     #e8f5fc;
            color:          #19a2e2;
        }

.toggleButton__role__primary.toggleButton__pressed:not(:hover) .toggleButton__title, .toggleButton__role__primary.toggleButton__pressed:not(:hover) .toggleButton__subLabel
        {
            color:  #19a2e2;
        }

.toggleButton__role__primary.toggleButton__pressed:not(:hover) .toggleButton__icon
        {
            fill:   #19a2e2;
        }

.toggleButton__iconPosition__left.toggleButton__role__primary .toggleButton__icon
        {
            margin-left:   -8px;
        }

.toggleButton__iconPosition__right.toggleButton__role__primary .toggleButton__icon
        {
            margin-right:  -8px;
        }

.toggleButton__role__secondary .toggleButton__flexContainer
    {
        height:         24px;
        border:         1px solid transparent;
        border-radius:  2px;
        padding:        0 8px;
    }

.toggleButton__role__secondary .toggleButton__title
    {
        font-size:      12px;
        line-height:    14px;
        text-transform: uppercase;
    }

.toggleButton__role__secondary .toggleButton__subLabel
    {
        display:    none;
    }

.toggleButton__role__secondary:not(.toggleButton__pressed) .toggleButton__title
        {
            color:  #98a8bc;
        }

.toggleButton__role__secondary:not(.toggleButton__pressed) .toggleButton__icon
        {
            fill:   #98a8bc;
        }

.toggleButton__iconPosition__left.toggleButton__role__secondary .toggleButton__icon
        {
            margin-left:   -4px;
        }

.toggleButton__iconPosition__right.toggleButton__role__secondary .toggleButton__icon
        {
            margin-right:  -4px;
        }

.toggleButton__role__subtle .toggleButton__flexContainer
    {
        height:         24px;
        border:         1px solid #19a2e2;
        border-radius:  2px;
        padding:        0 12px;
        background:     #ffffff;
    }

.toggleButton__role__subtle .toggleButton__title
    {
        font-size:      14px;
        line-height:    16px;
        color:          #19a2e2;
    }

.toggleButton__role__subtle .toggleButton__subLabel
    {
        display:        none;
    }

.toggleButton__role__subtle:not(.toggleButton__disabled):hover .toggleButton__flexContainer
        {
            background: #e8f5fc;
        }

.toggleButton__role__subtle.toggleButton__pressed .toggleButton__flexContainer
        {
            border-color: #19a2e2;
            background:   #19a2e2;
        }

.toggleButton__role__subtle.toggleButton__pressed .toggleButton__flexContainer:hover
            {
                background: #30abe4;
            }

.toggleButton__role__subtle.toggleButton__pressed .toggleButton__title
        {
            color: #ffffff;
        }

.toggleButton__role__subtle.toggleButton__pressed .toggleButton__icon
        {
            fill: #ffffff;
        }

.toggleButton__role__subtle .toggleButton__icon
    {
        fill:     #19a2e2;
    }

.toggleButton__iconPosition__left.toggleButton__role__subtle .toggleButton__icon
        {
            margin-left: -8px;
        }

.toggleButton__iconPosition__right.toggleButton__role__subtle .toggleButton__icon
        {
            margin-right: -8px;
        }

.toggleButton__disabled
{
    opacity:        0.5;
    cursor:         default;
    pointer-events: none;
}

.toggleButton__disabled .toggleButton__icon
    {
        fill: #4b627d;
    }

.toggleButton__disabled.toggleButton__role__subtle .toggleButton__icon .toggleButton__icon
        {
            fill: #30abe4;
        }

.toggleButton__disabled.toggleButton__role__subtle .toggleButton__icon.toggleButton__pressed .toggleButton__icon
        {
            fill: #ffffff;
        }

.toggleButton__iconPosition__left .toggleButton__icon
    {
        margin-right:   4px;
    }

.toggleButton__iconPosition__right .toggleButton__flexContainer
    {
        -webkit-box-orient: horizontal;
        -webkit-box-direction: reverse;
        -webkit-flex-direction: row-reverse;
            -ms-flex-direction: row-reverse;
                flex-direction: row-reverse;
    }

.toggleButton__iconPosition__right .toggleButton__icon
    {
        margin-left:    4px;
    }
