.datePicker__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.datePicker__default:last-child
    {
        margin-bottom:  0;
    }

.datePicker__default
{
    width:          320px;
}

.datePicker__default .datePicker__calendarHeader
    {
        color:              #19a2e2;
        font-size:          11px;
        line-height:        16px;
        text-transform:     uppercase;
        text-align:         center;
    }

.datePicker__default .datePicker__calendarHeader th
        {
            padding-bottom:    8px;
        }

.datePicker__default .datePicker__calendarHeader th:last-child
            {
                padding-right:  0;
            }

.datePicker__default .datePicker__calendarHeader th span
            {
                display:        block;
                position:       relative;
                top:            5px;
                margin-top:     -8px;
            }

.datePicker__default table
    {
        width:              100%;

        padding-top:        24px;
        padding-right:      8px;
        padding-bottom:     16px;
        padding-left:       8px;
    }

.datePicker__default td
    {
        padding: 0;
    }
