.checkbox__default
{
    position:       relative;
    margin:         0;
    margin-bottom:  16px;

    -webkit-box-sizing:     border-box;

            box-sizing:     border-box;
    width:          100%;
    max-width:      100%;
    min-width:      0;
}

/* in case this is a flexbox child */

.checkbox__default:last-child
    {
        margin-bottom:  0;
    }

.checkbox__default
{
    padding-top:    8px;
    padding-bottom: 8px;

    width:          auto;
}

.checkbox__default .checkbox__input /* the (hidden) native input */
    {
        position:   absolute;
        left:       -9999px;
    }

.checkbox__default .checkbox__label /* label is the styled checkbox/radio */
    {
        display:        -webkit-box;
        display:        -webkit-flex;
        display:        -ms-flexbox;
        display:        flex;
        -webkit-box-align:    center;
        -webkit-align-items:    center;
            -ms-flex-align:    center;
                align-items:    center;

        position:       relative;

        height:         24px;

        cursor:         pointer;
    }

.checkbox__default .checkbox__label::before
        {
            content:        '';

            display:        block;
            position:       relative;

            -webkit-box-sizing:     border-box;

                    box-sizing:     border-box;
            width:          24px;
            height:         24px;

            border:         1px solid #97d5f1;
            border-radius:  2px;

            background:     #ffffff;
        }

.checkbox__default .checkbox__label::after
        {
            content:    '';

            display:    none;
            position:   absolute;

            top:        0;
            left:       0;

            width:      24px;
            height:     24px;
        }

.checkbox__default .checkbox__labelContent
    {
        display:        block;
        margin-left:    8px;
    }

.checkbox__default .checkbox__labelText
    {
        color:  #4b627d;
    }

.checkbox__default .checkbox__input:checked + .checkbox__label::before, .checkbox__default .checkbox__input:focus + .checkbox__label::before, .checkbox__default.checkbox__fakeHovered .checkbox__label::before
    {
        border-color:   #19a2e2;
    }

.checkbox__default .checkbox__input:focus + .checkbox__label::before, .checkbox__default.checkbox__fakeHovered .checkbox__label::before
    {
        -webkit-box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
                box-shadow: 0 0 0 2px rgba(25, 162, 226, 0.3);
    }

.checkbox__error .checkbox__label::before
    {
        border: 1px solid #ff5460 !important;
    }

.checkbox__error .checkbox__input:focus + .checkbox__label::before, .checkbox__error.checkbox__fakeHovered .checkbox__label::before
    {
        -webkit-box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
                box-shadow: 0 0 0 2px rgba(255, 84, 96, 0.3);
    }

.checkbox__disabled .checkbox__label
    {
        cursor: default;
    }

.checkbox__disabled .checkbox__label::before
        {
            border-color:   #c7d0db !important;
        }

.checkbox__disabled .checkbox__labelText
    {
        color:  #c7d0db;
    }

.checkbox__default .checkbox__input:checked + .checkbox__label::before
        {
            background: #19a2e2;
        }

.checkbox__default .checkbox__input:checked + .checkbox__label::after
        {
            display:                block;

            background:             url(data:image/svg+xml;base64,);
            background-position:    center;
            background-size:        24px;
            background-repeat:      no-repeat;
        }

.checkbox__error .checkbox__input:checked + .checkbox__label::before
    {
        background: #ff5460;
    }

.checkbox__disabled .checkbox__input:checked + .checkbox__label::before
    {
        background:   #c7d0db;
    }
